import { Subject } from 'rxjs';
import {
  Component,
  ChangeDetectionStrategy,
  ViewEncapsulation,
  Input,
  SimpleChanges,
  OnInit,
  OnDestroy
} from "@angular/core";
import { CalendarEvent, CalendarDateFormatter, CalendarMonthViewDay  } from "angular-calendar";
import { subDays, addDays } from "date-fns";
import { TDLPlanTimesheetResource } from '../../models/tdlplan-sheet.model';
import { CustomDateFormatter } from '../../models/custom-date-formatter.provider';
import { ResourceService } from '../../../tdl/services/resource.service';
import { CalendarHour } from '../../models/calhours.model';
import { colors } from '../../models/colors';
import { TDLPlanHoursQuery } from '../../models/planhr-query.model';
import { ResourceWorkloadView } from '../../../tdl/models/res-wkload.model';

declare var jQuery: any;

@Component({
  selector: "[res-workcal]",
  changeDetection: ChangeDetectionStrategy.OnPush,
  styleUrls: ["./wkload-calendar.style.scss"],
  encapsulation: ViewEncapsulation.None,
  templateUrl: "wkload-calendar.template.html",
  providers: [
    {
      provide: CalendarDateFormatter,
      useClass: CustomDateFormatter
    },
    ResourceService
  ]
})

export class ResourceWorkLoadCalendarPopup implements OnInit, OnDestroy  {

  public resmodel:TDLPlanTimesheetResource;
  public querymodel:TDLPlanHoursQuery;
  public workloadmodel:ResourceWorkloadView[]=[];

  public utilization: number = 0.0;
  public totalAvailable: number = 0.0;
  public totalUsed:number = 0.0;

  @Input() resource:TDLPlanTimesheetResource;
  @Input() query:TDLPlanHoursQuery;

  public view: string = "month";

  public viewDate: Date = new Date();

  public busyloadResourceWorkload: any;
  public resourceCalHours:CalendarHour[] = [];

  public events: CalendarEvent[] = [
    {
      start: new Date("2018-01-08"),
      end: new Date("2018-01-10"),
      title: "One day excluded event",
      color: colors.red,
      meta: {
        id: 1
      }
    },
    {
      start: new Date("2018-01-01"),
      end: new Date("2018-01-09"),
      title: "Multiple weeks event",
      color: colors.blue,
      meta: {
        id: 1
      }
    }
  ];

  // exclude weekends
  public excludeDays: number[] = [0, 6];
  public refresh: Subject<any> = new Subject();

  constructor(
    private resourceService: ResourceService
  ){

  }

  ngOnInit(): void {
  }

  ngOnDestroy() {
    if(this.busyloadResourceWorkload){
      this.busyloadResourceWorkload.unsubscribe();
    }
  }

  public beforeMonthViewRender({ body }: { body: CalendarMonthViewDay[] }): void {
    body.forEach(day => {
      if (day.date.getDate() % 2 === 1 && day.inMonth) {
        day.cssClass = 'odd-cell';
      }
    });
  }

  public isOverload(day){
    if(this.getDayEventTotal(day) >= 8) {
      return true;
    }
    return false;
  }

  public getDayEventTotal(day){
    let retval:number = 0;
    if(day.events.length > 0){
      retval = parseInt(day.events[0].title);
    }
    return retval;
  }

  private getFormattedDate(d:Date): string {
    return `${d.getFullYear()}-${d.getMonth() + 1}-${d.getDate()}`;
  }

  public skipWeekends(direction: "back" | "forward"): void {
    if(this.view === "month"){
      this.querymodel.weekEnding = this.getFormattedDate(addDays(this.viewDate, -this.viewDate.getDay() + 6));
      this.getResourceWorkload(this.querymodel);
    }
    if (this.view === "day") {
      if (direction === "back") {
        while (this.excludeDays.indexOf(this.viewDate.getDay()) > -1) {
          this.viewDate = subDays(this.viewDate, 1);
        }
      } else if (direction === "forward") {
        while (this.excludeDays.indexOf(this.viewDate.getDay()) > -1) {
          this.viewDate = addDays(this.viewDate, 1);
        }
      }
    }
  }

  private refreshView(): void {
      this.refresh.next();
  }

  private appendDailyHour(weekEnding:Date, numDaystoSubtract:number, hour:number){
    let ch:CalendarHour = new CalendarHour();
    ch.curDate = addDays(weekEnding, numDaystoSubtract);
    ch.hours = hour;
    if(ch.curDate.getMonth() == new Date(weekEnding).getMonth()){
      this.totalUsed += hour;
      this.resourceCalHours.push(ch);
      this.events.push({
        start: ch.curDate,
        end: ch.curDate,
        title: ch.hours.toString(),
        color: colors.red
      });
    }

  }

  private processWorkLoadtoCalendarEvents(){
    this.resourceCalHours = [];
    this.events = [];

    this.totalAvailable = 0.0;
    this.totalUsed = 0.0;

    // https://github.com/kalmecak/moment-business-days
    let momentbiz = require('moment-business-days');
    let totalWorkingDays = momentbiz(this.viewDate).monthBusinessDays().length;
    this.totalAvailable = totalWorkingDays * 8.0;

    for(let res of this.workloadmodel){
      this.appendDailyHour(res.weekEnding, -6, res.sun);
      this.appendDailyHour(res.weekEnding, -5, res.mon);
      this.appendDailyHour(res.weekEnding, -4, res.tue);
      this.appendDailyHour(res.weekEnding, -3, res.wed);
      this.appendDailyHour(res.weekEnding, -2, res.thu);
      this.appendDailyHour(res.weekEnding, -1, res.fri);
      this.appendDailyHour(res.weekEnding, 0, res.sat);
    }
    this.utilization = (this.totalAvailable != 0) ? (this.totalUsed/this.totalAvailable) : 0.0;
    this.refreshView();
  }

  public getResourceWorkload(query:any){
    let handler = this;
    this.busyloadResourceWorkload = this.resourceService.getResourceWorkloadForMonth(query).subscribe(
      data => {
        if (data) {
          handler.workloadmodel = data;
          handler.processWorkLoadtoCalendarEvents();
        }
      },
      error => {
      }
    );
  }

  ngOnChanges(changes: SimpleChanges) {
    for (let propName in changes) {
      if(propName === "resource"){
        let chg = changes[propName];
        if (chg.currentValue) {
          this.resmodel = chg.currentValue;
        }
      }
      if(propName === "query"){
        let chg = changes[propName];
        if (chg.currentValue) {
          this.querymodel = chg.currentValue;
          this.getResourceWorkload(this.querymodel);
        }
      }
    }
  }

  public reset(flag) {
    jQuery("#res-workcal").modal("hide");
  }
}
