import { AfterViewInit, Component, EventEmitter, Input, OnInit, Output, OnChanges, OnDestroy, SimpleChanges, ViewEncapsulation } from '@angular/core';
import { saveAs } from 'file-saver';
import * as _ from 'lodash';
import { IMyDateModel, IMyDpOptions } from 'mydatepicker';
import { AlertComponent } from 'ngx-bootstrap';
import { NGXLogger } from "ngx-logger";
import { isNil } from 'ramda';
import { Subscription, Subject } from 'rxjs';
import * as XLSX from 'xlsx';
import { AuthService } from '../../../../login/services/auth.service';
import { DateService } from '../../../../shared/service/date.service';
import { TDLResourceView } from '../../../../tdl/models/tdlresourceview.model';
import { LaborRatePeriod } from '../../../models/labrate-period.model';
import { ResourceTDLQuery } from '../../../models/restdl-query.model';
import { WeeklyTDLResourcePlanFilter, WeeklyTDLResourcePlanFilterData } from '../../../models/wkltdl-resource-filter.model';
import { WeeklyTDLPlanTimesheet, WeeklyTDLPlanTimesheetResource } from '../../../models/wkly-tdlplansheet.model';
import { TDLPlanHoursAuditLog } from '../../../models/tdl-planhrs-audit.model';
import { PlanSheetService } from '../../../services/plan-sheet.service';

const TIMESHEET_DISPLAY_PERIOD = 49; // 7 WEEKS or 49 DAYS

declare var jQuery: any;

@Component({
  selector: "tdlplan-wklysheet",
  templateUrl: "./tdlplan-wklysheet.template.html",
  styleUrls: ["./tdlplan-wklysheet.style.scss"],
  encapsulation: ViewEncapsulation.None
})
export class TDLPlanWeeklySheetComponent implements OnInit, OnChanges, OnDestroy, AfterViewInit {
  public curtimeSheet: WeeklyTDLPlanTimesheet;
  public alerts: Array<Object>;
  public busy: Subscription;
  public data: any[];
  public wktdlresfilter: WeeklyTDLResourcePlanFilter = new WeeklyTDLResourcePlanFilter();
  public wktdlresfilterdata:WeeklyTDLResourcePlanFilterData = new WeeklyTDLResourcePlanFilterData();
  public auditlog: TDLPlanHoursAuditLog[] = [];

  myDatePickerOptions: IMyDpOptions = {
    dateFormat: 'mm/dd/yyyy',
    firstDayOfWeek: 'su',
    sunHighlight: false,
    satHighlight: true,
    showClearDateBtn: false
} ;

  @Input() timesheet: WeeklyTDLPlanTimesheet;
  @Input() labrateperiod: LaborRatePeriod[];
  @Input() resources: TDLResourceView[];
  @Input() canEdit: boolean;
  @Input() isEditing: boolean;
  @Input() TDLId: number;
  @Input() parentSaveTimesheet:Subject<boolean>;
  @Output() beginEdit = new EventEmitter();
  @Output() endEdit = new EventEmitter<boolean>();
  @Output() onApprove = new EventEmitter<boolean>();
  @Output() delete = new EventEmitter();
  @Output() dateChange: EventEmitter<any> = new EventEmitter();

  public busyDownloadAuditLog: Subscription;

  public weekEndingModel: any = {};
  public ShowWorkLoadGraph:boolean;

  public editableText = '40';
  public selResource:WeeklyTDLPlanTimesheetResource;
  public selQuery:ResourceTDLQuery;

  public isApprovalValid: boolean = false;
  public approvalStatus: string;
  public approvalDate: string;
  public approvalBy: string;

  public sortResultsBy = "name";
  public sortResultsOrder = "asc";
  public pageNumResults: number = 1;

  public columns:Array<any> = [
    {name: 'flag', type: 'none'},
    {name: 'name', type: 'input', filtering: {filterString: '', columnName: 'name', placeholder: ''}}
  ];

  constructor(private authService: AuthService, private planSheetService: PlanSheetService, private logger: NGXLogger) {
    this.alerts = [];
  }

  ngOnInit() {
    this.ShowWorkLoadGraph = false;

    this.busy = this.parentSaveTimesheet.subscribe(event => {
      if(event){
        this.addSuccess();
      }
    });
  }

  ngAfterViewInit() {
    for(let col of this.columns){
        if(col.filtering){
            this.cosmeticFix('#filter-' + col.name);
        }
    }
  }

  ngOnDestroy(){
    if(this.busyDownloadAuditLog){
      this.busyDownloadAuditLog.unsubscribe();
    }
  }

  public cosmeticFix(elementID: any){
    // The code below for cosmetic fix - Blue box now surrounds the icon on the search company text box
    let filterInput = jQuery(elementID);
    filterInput
        .focus((e) => {
            jQuery(e.target).closest('.input-group').addClass('focus');
        })
        .focusout((e) => {
            jQuery(e.target).closest('.input-group').removeClass('focus');
        });
  }

  public setWeekEndingModel() {
    if (this.curtimeSheet.weekEnding) {
      this.weekEndingModel = {
        date: {
          year: this.curtimeSheet.weekEnding.getFullYear(),
          month: this.curtimeSheet.weekEnding.getMonth() + 1,
          day: this.curtimeSheet.weekEnding.getDate()
        }
      };
    }
  }

  public getWeekName(index: number){
    let startWeekEnding = DateService.getThisSaturday(); // moment().day('Saturday').toDate();
    if(!isNil(this.curtimeSheet)){
      startWeekEnding = DateService.resetTime(this.curtimeSheet.weekEnding); // moment(this.curtimeSheet.weekEnding).toDate();
    }
    // return moment(startWeekEnding).day('Saturday').add(index * 7, 'days').format("MM/DD");
    return DateService.getFormattedText(
        DateService.getDateByAddingDaysFromDate(
          DateService.getSaturday(startWeekEnding), index * 7
        ), "MM/DD"
      );
  }

  public updateApprovalStatusInfo(){
    this.isApprovalValid = false;
    switch(this.curtimeSheet.approvalStatus){
      case 'APRV':
        this.approvalStatus = "Approved";
        this.isApprovalValid  = true;
        break;

      case 'UNAP':
        this.approvalStatus = "Unapproved";
        this.isApprovalValid  = true;
        break;
    }

    if(this.isApprovalValid){
      this.approvalBy = this.curtimeSheet.approvedBy;
      this.approvalDate = DateService.getFormattedText(this.curtimeSheet.approvedDt, "MMM DD, YYYY h:mm:ss A");//moment(this.curtimeSheet.approvedDt).format('MMM DD, YYYY h:mm:ss A');
    }
  }

  ngOnChanges(changes: SimpleChanges) {
    for (let propName in changes) {
      if(propName === "timesheet"){
        let chg = changes[propName];
        if (chg.currentValue) {
          this.curtimeSheet = chg.currentValue;
          this.data = this.curtimeSheet.ptsresources;
          this.updateApprovalStatusInfo();
          this.setWeekEndingModel();
        }
      }
    }
  }

  public openSplitEditor(){
    jQuery('#splitbtn').click();
  }

  public addSuccess(): void {
    this.alerts.push({
      type: 'info',
      msg: `Timesheet Successfully Saved (added: ${new Date().toLocaleTimeString()})`,
      timeout: 5000
    });
  }

  public onClosed(dismissedAlert: AlertComponent): void {
    this.alerts = this.alerts.filter(alert => alert !== dismissedAlert);
  }

  public getTotalResourceRowCost(r:any){
    let list = [0, 1, 2, 3, 4, 5, 6];
    let totalCost = 0.0;
    for (let i in list) {
      totalCost += Number(r.weekhours[i]) * Number(r.weekrates[i]);
    }
    return totalCost;
  }

  public getTotalResourceRowHours(r:any){
    let list = [0, 1, 2, 3, 4, 5, 6];
    let totalHours = 0.0;
    for (let i in list) {
      totalHours += Number(r.weekhours[i]);
    }
    return totalHours;
  }

  public splitHours(hours:number, r:any){
    r.weekhours[0] = Number(hours);
    r.weekhours[1] = Number(hours);
    r.weekhours[2] = Number(hours);
    r.weekhours[3] = Number(hours);
    r.weekhours[4] = Number(hours);
    r.weekhours[5] = Number(hours);
    r.weekhours[6] = Number(hours);
  }

  public isNegativeBalance(balance){
    if(balance < 0){
      return true;
    }
    return false;
  }

  public isCurrentWeek(weekName: string){
    let curWeekName =  DateService.getFormattedText(DateService.getThisSaturday(), "MM/DD"); // moment().day('Saturday').format("MM/DD");
    if(curWeekName == weekName){
      return true;
    }
    return false;
  }

  // Control will take back 7 weeks.
  public gotoPrevDisplayPeriod(){
    if (this.curtimeSheet && this.curtimeSheet.weekEnding) {
      const d = this.curtimeSheet.weekEnding;
      const day = d.getDay();
      if(day < 0 && day > 7){
        return;
      }
      if (day !== 6) {
        d.setDate(d.getDate() + (6 - day) - TIMESHEET_DISPLAY_PERIOD);
      }else{
        d.setDate(d.getDate() - TIMESHEET_DISPLAY_PERIOD);
      }
      this.curtimeSheet.weekEnding = this.getSaturday(d);
    } else {
      this.curtimeSheet.weekEnding = null;
    }
    this.setWeekEndingModel();
    this.dateChange.emit(this.weekEndingModel);
  }

  public gotoNextDisplayPeriod(){
    if (this.curtimeSheet && this.curtimeSheet.weekEnding) {
      const d = this.curtimeSheet.weekEnding;
      const day = d.getDay();
      if(day < 0 && day > 7){
        return;
      }
      if (day !== 6) {
        d.setDate(d.getDate() + (6 - day) + TIMESHEET_DISPLAY_PERIOD);
      }else {
        d.setDate(d.getDate() +  TIMESHEET_DISPLAY_PERIOD);
      }
      this.curtimeSheet.weekEnding = this.getSaturday(d);
    } else {
      this.curtimeSheet.weekEnding = null;
    }
    this.setWeekEndingModel();
    this.dateChange.emit(this.weekEndingModel);
  }

  public getSaturday(d:any): Date {
    let ind:Date = DateService.resetTime(d); // moment(d).toDate();
    let retd:Date = DateService.getSaturday(d); // moment(d).day('Saturday').startOf('day').toDate();
    // console.log('tdlplan-weklysheet - saturday of ' + ind.toString() + ' is ' + retd.toString() ) ;
    return retd;
  }

  public onWeekEndingDateChanged(event: IMyDateModel) {
    if (event.jsdate) {
      this.curtimeSheet.weekEnding = this.getSaturday(event.jsdate);
    } else {
      this.curtimeSheet.weekEnding = null;
    }
    this.setWeekEndingModel();
    this.dateChange.emit(this.weekEndingModel);
  }

  public hasRole(rolename): boolean{
    return this.authService.jwt_hasRole("ROLE_" + rolename);
  }

  public saveTimeSheet(){
    jQuery('#tbl-tdlplansheet').parsley().validate();
    if (jQuery('#tbl-tdlplansheet').parsley().isValid()) {
      this.endEdit.emit(true);
    }
  }

  private s2ab(s) {
    let buf = new ArrayBuffer(s.length);
    let view = new Uint8Array(buf);
    for (let i=0; i!== s.length; ++i) view[i] = s.charCodeAt(i) & 0xFF;
    return buf;
  }

  public exportAuditLogReport(data:any, filename, sheetName){
    let ws:XLSX.WorkSheet = XLSX.utils.json_to_sheet(data);
    let wb:XLSX.WorkBook = XLSX.utils.book_new();
    XLSX.utils.book_append_sheet(wb, ws, sheetName);

    /* save to file */
    let wbout: string = XLSX.write(wb, { bookType: 'xlsx', type: 'binary' });
    saveAs(new Blob([this.s2ab(wbout)]), filename);
  }


  public downloadAuditLog(){
    let that = this;
    // console.log("TDL ID is " + this.TDLId)
    this.busyDownloadAuditLog = this.planSheetService.getTDLPlanHoursAuditLog(this.TDLId).subscribe(
      data => {
        if(data){
          that.auditlog = data;
          this.exportAuditLogReport(data, 'tdl-planhrs-audit.xlsx', 'AuditLog');
        }
      }, error => {}
    );
  }

  public onDeleteTimesheetResourceClick(timesheetResource: WeeklyTDLPlanTimesheetResource) {
    const i = this.curtimeSheet.ptsresources.indexOf(timesheetResource);
    this.curtimeSheet.ptsresources.splice(i, 1);
  }

  public onAddTimesheetResourceClick() {
    const newsheetResource = new WeeklyTDLPlanTimesheetResource();
    newsheetResource.weekhours = [0, 0, 0, 0, 0, 0, 0];
    this.curtimeSheet.ptsresources.push(newsheetResource);
  }

  public calcTotalHoursByTimesheetResource(timesheetResource: WeeklyTDLPlanTimesheetResource): number {
    let result = 0;
    for (let i = 0; i < timesheetResource.weekhours.length; i++) {
      result += timesheetResource.weekhours[i];
    }
    return result;
  }

  public getTotalTDLHoursByTimesheetResource(timesheetResource: WeeklyTDLPlanTimesheetResource): number {
    let result = 0;
    return result;
  }

  public saveResourceTimeSheetEntry(timesheetResource: WeeklyTDLPlanTimesheetResource)
  {
    // Total hours before user clicks check
    let resPlanHoursOriginal = timesheetResource.planHours;
    let weekTotal = this.calcTotalHoursByTimesheetResource(timesheetResource);
    let resUpdatedPlanHours = timesheetResource.planHours - timesheetResource.weekTotal + weekTotal;
    timesheetResource.weekTotal = weekTotal;
    timesheetResource.planHours = resUpdatedPlanHours;
    timesheetResource.balanceHours = timesheetResource.budgetHours - timesheetResource.planHours;
  }


  public calcTotalCostByTimesheetResource(timesheetResource: WeeklyTDLPlanTimesheetResource): number {
    let result = 0;

    if(!isNil(timesheetResource)){
      for (let i = 0; i < timesheetResource.weekhours.length; i++) {
        result += timesheetResource.weekhours[i] * timesheetResource.weekrates[i];
      }
    }
    return result;
  }

  public getTotalTDLCostByTimesheetResource(timesheetResource: WeeklyTDLPlanTimesheetResource, rate: number): number {
    let result = 0;

    return result;
  }

  public calcTotalHoursByDay(dayNumber: number): number {
    let result = 0;

    if(!isNil(this.curtimeSheet)){
      for (let i = 0; i < this.curtimeSheet.ptsresources.length; i++) {
        result += this.curtimeSheet.ptsresources[i].weekhours[dayNumber];
      }
    }
    return result;
  }

  public calcTotalHours(): number {
    let result = 0;

    if(!isNil(this.curtimeSheet)){
      for (let i = 0; i < this.curtimeSheet.ptsresources.length; i++) {
        result += this.calcTotalHoursByTimesheetResource(
          this.curtimeSheet.ptsresources[i]
        );
      }
    }

    return result;
  }

  public calcTotalCost(): number {
    let result = 0;

    if(!isNil(this.curtimeSheet)){
      for (let i = 0; i < this.curtimeSheet.ptsresources.length; i++) {
        result += this.calcTotalCostByTimesheetResource(this.curtimeSheet.ptsresources[i]);
      }
    }

    return result;
  }

  public getFormattedDate(d:Date): string {
    return `${d.getFullYear()}-${d.getMonth() + 1}-${d.getDate()}`;
  }

  public displayWorkLoadGraph(selResource:WeeklyTDLPlanTimesheetResource, weekEnding:any){
    this.ShowWorkLoadGraph = true;
    this.selResource = selResource;
    let query = new ResourceTDLQuery();
    query.resourceID = selResource.resourceID;
    query.weekEnding = this.getFormattedDate(weekEnding);
    this.selQuery = query;
  }

  public refreshResourceFilterObject() {
    this.wktdlresfilterdata = null;
    this.wktdlresfilterdata = new WeeklyTDLResourcePlanFilterData();

    this.wktdlresfilterdata.nameFilter = this.wktdlresfilter.nameFilter;
    // this.saveSearchCriteriaToLocalStorage();
  }

  public onChangeTable(filterinfo):void {
      if(filterinfo){
          if(filterinfo.columnName === "name"){
              this.wktdlresfilter.nameFilter = filterinfo.filterString;
              this.wktdlresfilter.nameFilter = this.wktdlresfilter.nameFilter.trim();
          }
          if(filterinfo.refresh){
              this.refreshResourceFilterObject();
          }
      }
  }

  public FilterSelectionChanged(event, filterColumnName){
  }

  public ApplyFilter(){
      this.refreshResourceFilterObject();
  }

  public updateFilterTextBox(colName, colText){
      let rowIndex = _.findIndex(this.columns, function (o) { return (o.name && o.name.toString() === colName);});
      if(rowIndex !== -1) {
          let selRow = this.columns[rowIndex];
          if(selRow.filtering){
              selRow.filtering.filterString = colText;
          }
      }
  }
}
