import { Component, Input, Output, OnInit, EventEmitter, ViewEncapsulation, SimpleChanges, OnDestroy } from "@angular/core";
import { IMyDateModel, IMyDpOptions } from "mydatepicker";
import { AlertComponent } from 'ngx-bootstrap';
import { Subscription, Subject } from 'rxjs';
import * as _ from "lodash";
import { isNil } from 'ramda';
import { TDLResourceView } from '../../../../tdl/models/tdlresourceview.model';
import { AuthService } from '../../../../login/services/auth.service';
import { WeeklyResourcePlanTimesheet, WeeklyResourcePlanTimesheetProject } from '../../../models/wkly-resplansheet.model';
import { PlanSheetService } from '../../../services/plan-sheet.service';
import { SendEmailQuery } from '../../../models/emailquery.model';
import { DateService } from '../../../../shared/service/date.service';
import * as XLSX from 'xlsx';
import { saveAs } from 'file-saver';
import { TDLPlanHoursAuditLog } from '../../../models/tdl-planhrs-audit.model';

declare var jQuery: any;

@Component({
  selector: "resplan-wklysheet",
  templateUrl: "./resplan-wklysheet.template.html",
  styleUrls: ["./resplan-wklysheet.style.scss"],
  encapsulation: ViewEncapsulation.None
})
export class ResourcePlanWeeklySheetComponent implements OnInit, OnDestroy {
  public curtimeSheet: WeeklyResourcePlanTimesheet;
  public alerts: Array<Object>;
  public busy: Subscription;
  public busySendEmail: Subscription;
  public showwbsCode: boolean;
  public numweekstoDisplay:number[]=[];
  public busyDownloadAuditLog: Subscription;
  public auditlog: TDLPlanHoursAuditLog[] = [];

  myDatePickerOptions: IMyDpOptions = {
    dateFormat: 'mm/dd/yyyy',
    firstDayOfWeek: 'su',
    sunHighlight: false,
    satHighlight: true,
    showClearDateBtn: false
} ;

  @Input() timesheet: WeeklyResourcePlanTimesheet;
  @Input() canEdit: boolean;
  @Input() isEditing: boolean;
  @Input() resourceid: number;
  @Input() tdlbreakhours:TDLResourceView[];
  @Input() parentSaveTimesheet:Subject<boolean>;
  @Output() beginEdit = new EventEmitter();
  @Output() endEdit: EventEmitter<any> = new EventEmitter();
  @Output() delete = new EventEmitter();
  @Output() dateChange: EventEmitter<any> = new EventEmitter();

  public showAlertFlag: boolean;
  public resourcetdlbreakhours: TDLResourceView[]=[];
  public weekEndingModel: any = {};

  constructor(private authService: AuthService,
        private planSheetService: PlanSheetService) {
    this.alerts = [];
    this.showwbsCode = false;
    this.showAlertFlag = false;
  }

  ngOnInit() {
    // console.log('ngOnInit - resplan-wklysheet.component - started');
    this.busy = this.parentSaveTimesheet.subscribe(event => {
      if(event){
        this.addSuccess();
      }
    });
    // console.log('ngOnInit - resplan-wklysheet.component - stopped');
  }

  ngOnDestroy(){
    if(this.busy){
      this.busy.unsubscribe();
    }
    if(this.busySendEmail){
      this.busySendEmail.unsubscribe();
    }
    if(this.busyDownloadAuditLog){
      this.busyDownloadAuditLog.unsubscribe();
    }
  }

  public setWeekEndingModel() {
    if (this.curtimeSheet.weekEnding) {
      this.weekEndingModel = {
        date: {
          year: this.curtimeSheet.weekEnding.getFullYear(),
          month: this.curtimeSheet.weekEnding.getMonth() + 1,
          day: this.curtimeSheet.weekEnding.getDate()
        }
      };
    }
  }

  public getWeekName(index: number){
    // let startWeekEnding = moment().day('Saturday').toDate();
    // if(!isNil(this.curtimeSheet)){
    //   startWeekEnding = moment(this.curtimeSheet.weekEnding).toDate();
    // }
    // return moment(startWeekEnding).day('Saturday').add(index * 7, 'days').format("MM/DD");

    let startWeekEnding = DateService.getThisSaturday(); // moment().day('Saturday').toDate();
    if(!isNil(this.curtimeSheet)){
      startWeekEnding = DateService.resetTime(this.curtimeSheet.weekEnding); // moment(this.curtimeSheet.weekEnding).toDate();
    }
    return DateService.getFormattedText(
        DateService.getDateByAddingDaysFromDate(
          DateService.getSaturday(startWeekEnding), index * 7
        ), "MM/DD"
      );
  }

  // public getBalanceHours(tdlID:number){
  //   let rowIndex = _.findIndex(this.resourcetdlbreakhours, function(o) {
  //     return o.tdlID === tdlID;
  //   });
  //   if (rowIndex !== -1) {
  //     let balancehours = this.resourcetdlbreakhours[rowIndex].budgetHours - this.resourcetdlbreakhours[rowIndex].planHours;
  //     return balancehours;
  //   }
  //   return 0.0;
  // }

  public getBalanceHours(ptsprojects:WeeklyResourcePlanTimesheetProject){
    let totalPeriodPlanHours = 0.0;
    let totalPeriodActualHours = 0.0;
    let balanceHours = 0.0;
    for (let i = 0; i < this.curtimeSheet.numWeeks; i++) {
      totalPeriodPlanHours += ptsprojects.weekhours[i];
      totalPeriodActualHours += ptsprojects.actweekhours[i];
    }
    balanceHours = totalPeriodPlanHours - totalPeriodActualHours;
    return balanceHours;

    // let rowIndex = _.findIndex(this.resourcetdlbreakhours, function(o) {
    //   return o.tdlID === tdlID;
    // });
    // if (rowIndex !== -1) {
    //   let balancehours = this.resourcetdlbreakhours[rowIndex].budgetHours - this.resourcetdlbreakhours[rowIndex].planHours;
    //   return balancehours;
    // }
    // return 0.0;
  }

  ngOnChanges(changes: SimpleChanges) {
    for (let propName in changes) {
      if(propName === "timesheet"){
        let chg = changes[propName];
        if (chg.currentValue) {
          this.curtimeSheet = chg.currentValue;
          this.numweekstoDisplay = [];
          for (let i = 0; i < this.curtimeSheet.numWeeks; i++) {
            this.numweekstoDisplay.push(i);
          }
          this.setWeekEndingModel();
        }
      }
      if(propName === "tdlbreakhours"){
        let chg = changes[propName];
        if (chg.currentValue) {
          this.resourcetdlbreakhours = chg.currentValue;
        }
      }
    }
  }

  public addSuccess(): void {
    this.showAlertFlag = true;
    this.alerts.push({
      type: 'info',
      msg: `Timesheet Successfully Saved (added: ${new Date().toLocaleTimeString()})`,
      timeout: 5000
    });
  }

  public onClosed(dismissedAlert: AlertComponent): void {
    this.alerts = this.alerts.filter(alert => alert !== dismissedAlert);
    this.showAlertFlag = false;
  }

  public openSplitEditor(){
    jQuery('#splitbtn').click();
  }

  public isNegativeBalance(balance){
    if(balance < 0){
      return true;
    }
    return false;
  }

  public splitHours(hours:number, r:any){
    let dailyhrs = hours/5;
    r.hours[1] = dailyhrs;
    r.hours[2] = dailyhrs;
    r.hours[3] = dailyhrs;
    r.hours[4] = dailyhrs;
    r.hours[5] = dailyhrs;
  }

  public saveTimeSheet(){
    jQuery('#tbl-plansheet').parsley().validate();
    if (jQuery('#tbl-plansheet').parsley().isValid()) {
      this.endEdit.emit(this.curtimeSheet);
    }
  }

  public hasRole(rolename): boolean{
    return this.authService.jwt_hasRole("ROLE_" + rolename);
  }

  public getFormattedDate(): string {
    if (this.curtimeSheet.weekEnding) {
      const we = new Date(this.curtimeSheet.weekEnding);
      return `${we.getMonth() + 1}/${we.getDate()}/${we.getFullYear()}`;
    } else {
      return null;
    }
  }

  public gotoPrevPeriod(){
    if (this.curtimeSheet && this.curtimeSheet.weekEnding) {
      const d =  DateService.getDateBySubtractingMonthsFromDate( DateService.getStartOfMonthFromGivenDate(this.curtimeSheet.weekEnding), 1);//moment(this.curtimeSheet.weekEnding).startOf('month').subtract(1, 'month').toDate();
      this.curtimeSheet.weekEnding = this.getPerformancePeriodStartWeekEnd(d);
    } else {
      this.curtimeSheet.weekEnding = null;
    }
    this.setWeekEndingModel();
    this.dateChange.emit(this.weekEndingModel);
  }

  public gotoNextPeriod(){
    if (this.curtimeSheet && this.curtimeSheet.weekEnding) {
      const d = DateService.getDateByAddingMonthsFromDate( DateService.getStartOfMonthFromGivenDate(this.curtimeSheet.weekEnding), 1);// moment(this.curtimeSheet.weekEnding).startOf('month').add(1, 'month').toDate();
      this.curtimeSheet.weekEnding = this.getPerformancePeriodStartWeekEnd(d);
    } else {
      this.curtimeSheet.weekEnding = null;
    }
    this.setWeekEndingModel();
    this.dateChange.emit(this.weekEndingModel);
  }

  public getPerformancePeriodStartWeekEnd(selDate:Date){
    let startWeekEnding:Date = DateService.getFirstSaturdayOfMonthFromGivenDate(selDate);
    // let ind:Date = moment(selDate).toDate();
    // let startWeekEnding = moment(selDate).startOf('month').day('Saturday').toDate();
    // console.log('resplan-wklysheet - first start weekend of month of ' + ind.toString() + ' is ' + startWeekEnding.toString() ) ;
    return startWeekEnding;
  }

  public getPerformancePeriodEndWeekEnd(selDate:Date){
    let endWeekEnding = DateService.getLastSaturdayOfMonthFromGivenDate(selDate); // moment(selDate).endOf('month').day('Saturday').toDate();
    if(DateService.getMonthOfDate(endWeekEnding) !== DateService.getMonthOfDate(selDate)){
      // endWeekEnding = moment(endWeekEnding).subtract(7, 'days').toDate();
      endWeekEnding = DateService.getDateBySubtractingDaysFromDate(endWeekEnding, 7);
    }
    return endWeekEnding;
    // let endWeekEnding = moment(selDate).endOf('month').day('Saturday').toDate();
    // if(moment(endWeekEnding).month() !== moment(selDate).month()){
    //   endWeekEnding = moment(endWeekEnding).subtract(7, 'days').toDate();
    // }
    // return endWeekEnding;
  }

  public onWeekEndingDateChanged(event: IMyDateModel) {
    if (event.jsdate) {
      const d = event.jsdate;
      this.curtimeSheet.weekEnding = this.getPerformancePeriodStartWeekEnd(d);
    } else {
      this.curtimeSheet.weekEnding = null;
    }
    this.setWeekEndingModel();
    this.dateChange.emit(this.weekEndingModel);
  }

  public calcTotalHoursByTimesheetResource(timesheetResource: WeeklyResourcePlanTimesheetProject): number {
    let result = 0;

    if(!isNil(timesheetResource)){
      for (let i = 0; i < this.curtimeSheet.numWeeks; i++) {
        result += timesheetResource.weekhours[i];
      }
    }
    return result;
  }

  public calcTotalHoursByDay(dayNumber: number): number {
    let result = 0;
    for (let i = 0; i < this.curtimeSheet.ptsprojects.length; i++) {
      result += this.curtimeSheet.ptsprojects[i].weekhours[dayNumber];
    }
    return result;
  }

  public calcTotalHours(): number {
    let result = 0;
    for (let i = 0; i < this.curtimeSheet.ptsprojects.length; i++) {
      result += this.calcTotalHoursByTimesheetResource(
        this.curtimeSheet.ptsprojects[i]
      );
    }
    return result;
  }

  public getChargeAuthorizationTimeRange(){
    let dateStart = DateService.getFormattedText(this.getPerformancePeriodStartWeekEnd(this.curtimeSheet.weekEnding), 'MMMM D, YYYY');// moment(this.getPerformancePeriodStartWeekEnd(this.curtimeSheet.weekEnding)).format('MMMM D, YYYY');
    let dateEnd = DateService.getFormattedText(this.getPerformancePeriodEndWeekEnd(this.curtimeSheet.weekEnding), 'MMMM D, YYYY');// moment(this.getPerformancePeriodEndWeekEnd(this.curtimeSheet.weekEnding)).format('MMMM D, YYYY');
    return dateStart + " to " + dateEnd;
  }

  public sendEmail() {
    let query: SendEmailQuery = new SendEmailQuery();
    query.resourceID = this.resourceid;
    query.timeRange = this.getChargeAuthorizationTimeRange();
    this.curtimeSheet.weekNames =[];
    for(let k = 0; k < this.curtimeSheet.numWeeks; k++){
      this.curtimeSheet.weekNames[k] = this.getWeekName(k);
    }
    query.curPlanSheet = this.curtimeSheet;
    let handler = this;
    this.busySendEmail = this.planSheetService.sendChargeAuthorizationEmail(query).subscribe(
      data => {
        if (data) {
          this.alerts = [];
          this.showAlertFlag = true;
          this.alerts.push({
            type: 'info',
            msg: data.value + `(added: ${new Date().toLocaleTimeString()})`,
            timeout: 5000
          });
        }
      },
      error => {
        this.alerts = [];
        this.showAlertFlag = true;
        this.alerts.push({
          type: 'info',
          msg: 'Error: sending email' + `(added: ${new Date().toLocaleTimeString()})`,
          timeout: 5000
        });
      });
  }

  private s2ab(s) {
    let buf = new ArrayBuffer(s.length);
    let view = new Uint8Array(buf);
    for (var i=0; i!== s.length; ++i) view[i] = s.charCodeAt(i) & 0xFF;
    return buf;
  }

  public exportAuditLogReport(data:any, filename, sheetName){
    let ws:XLSX.WorkSheet = XLSX.utils.json_to_sheet(data);
    let wb:XLSX.WorkBook = XLSX.utils.book_new();
    XLSX.utils.book_append_sheet(wb, ws, sheetName);

    /* save to file */
    let wbout: string = XLSX.write(wb, { bookType: 'xlsx', type: 'binary' });
    saveAs(new Blob([this.s2ab(wbout)]), filename);
  }


  public downloadAuditLog(){
    let that = this;
    this.busyDownloadAuditLog = this.planSheetService.getResourcePlanHoursAuditLog(this.resourceid, this.curtimeSheet.weekEnding).subscribe(
      data => {
        if(data){
          that.auditlog = data;
          this.exportAuditLogReport(data, 'res-planhrs-audit.xlsx', 'AuditLog');
        }
      }, error => {}
    );
  }
}
