import { Component, EventEmitter, Input, OnInit, Output, OnChanges, SimpleChanges, ViewEncapsulation } from '@angular/core';
import { IMyDateModel, IMyDpOptions } from 'mydatepicker';
import { AlertComponent } from 'ngx-bootstrap';
import { isNil } from 'ramda';
import { Subscription, Subject } from 'rxjs';
import { AuthService } from '../../../../login/services/auth.service';
import { TDLResourceView } from '../../../../tdl/models/tdlresourceview.model';
import { ResourceTDLQuery } from '../../../models/restdl-query.model';
import { TDLPlanTimesheet, TDLPlanTimesheetResource } from '../../../models/tdlplan-sheet.model';
import { DateService } from '../../../../shared/service/date.service';

const TIMESHEET_DISPLAY_PERIOD = 7; // 1 WEEK - 7 DAYS

declare var jQuery: any;

@Component({
  selector: "tdlplan-sheet",
  templateUrl: "./tdlplan-sheet.template.html",
  styleUrls: ["./tdlplan-sheet.style.scss"],
  encapsulation: ViewEncapsulation.None
})
export class TDLPlanSheetComponent implements OnInit, OnChanges {
  public curtimeSheet: TDLPlanTimesheet;
  public alerts: Array<Object>;
  public busy: Subscription;

  myDatePickerOptions: IMyDpOptions = {
    dateFormat: 'mm/dd/yyyy',
    firstDayOfWeek: 'su',
    sunHighlight: false,
    satHighlight: true,
    showClearDateBtn: false
} ;

  @Input() timesheet: TDLPlanTimesheet;
  @Input() resources: TDLResourceView[];
  @Input() canEdit: boolean;
  @Input() isEditing: boolean;
  @Input() parentSaveTimesheet:Subject<boolean>;
  @Output() beginEdit = new EventEmitter();
  @Output() endEdit = new EventEmitter<boolean>();
  @Output() onApprove = new EventEmitter<boolean>();
  @Output() delete = new EventEmitter();
  @Output() dateChange: EventEmitter<any> = new EventEmitter();

  public weekEndingModel: any = {};
  public ShowWorkLoadGraph:boolean;

  public editableText = '40';
  public selResource:TDLPlanTimesheetResource;
  public selQuery:ResourceTDLQuery;

  public isApprovalValid: boolean = false;
  public approvalStatus: string;
  public approvalDate: string;
  public approvalBy: string;

  constructor(private authService: AuthService) {
    this.alerts = [];
  }

  ngOnInit() {
    this.ShowWorkLoadGraph = false;

    this.busy = this.parentSaveTimesheet.subscribe(event => {
      if(event){
        this.addSuccess();
      }
    });
  }

  private setWeekEndingModel() {
    if (this.curtimeSheet.weekEnding) {
      this.weekEndingModel = {
        date: {
          year: this.curtimeSheet.weekEnding.getFullYear(),
          month: this.curtimeSheet.weekEnding.getMonth() + 1,
          day: this.curtimeSheet.weekEnding.getDate()
        }
      };
    }
  }

  public updateApprovalStatusInfo(){
    this.isApprovalValid = false;
    switch(this.curtimeSheet.approvalStatus){
      case 'APRV':
        this.approvalStatus = "Approved";
        this.isApprovalValid  = true;
        break;

      case 'UNAP':
        this.approvalStatus = "Unapproved";
        this.isApprovalValid  = true;
        break;
    }

    if(this.isApprovalValid){
      this.approvalBy = this.curtimeSheet.approvedBy;
      this.approvalDate = DateService.getFormattedText(this.curtimeSheet.approvedDt, "MMM DD, YYYY h:mm:ss A"); //moment(this.curtimeSheet.approvedDt).format('MMM DD, YYYY h:mm:ss A');
    }
  }

  ngOnChanges(changes: SimpleChanges) {
    for (let propName in changes) {
      if(propName === "timesheet"){
        let chg = changes[propName];
        if (chg.currentValue) {
          this.curtimeSheet = chg.currentValue;
          this.updateApprovalStatusInfo();
          this.setWeekEndingModel();
        }
      }
    }
  }

  public openSplitEditor(){
    jQuery('#splitbtn').click();
  }

  public addSuccess(): void {
    this.alerts.push({
      type: 'info',
      msg: `Timesheet Successfully Saved (added: ${new Date().toLocaleTimeString()})`,
      timeout: 5000
    });
  }

  public onClosed(dismissedAlert: AlertComponent): void {
    this.alerts = this.alerts.filter(alert => alert !== dismissedAlert);
  }

  public splitHours(hours:number, r:any){
    let dailyhrs = hours/5;
    r.hours[1] = dailyhrs;
    r.hours[2] = dailyhrs;
    r.hours[3] = dailyhrs;
    r.hours[4] = dailyhrs;
    r.hours[5] = dailyhrs;
  }

  public isNegativeBalance(balance){
    if(balance < 0){
      return true;
    }
    return false;
  }

  public gotoPrevWeek(){
    if (this.curtimeSheet && this.curtimeSheet.weekEnding) {
      const d = this.curtimeSheet.weekEnding;
      const day = d.getDay();
      if(day < 0 && day > 7){
        return;
      }
      if (day !== 6) {
        d.setDate(d.getDate() + (6 - day) - TIMESHEET_DISPLAY_PERIOD);
      }else{
        d.setDate(d.getDate() - TIMESHEET_DISPLAY_PERIOD);
      }
      this.curtimeSheet.weekEnding = this.getSaturday(d);
    } else {
      this.curtimeSheet.weekEnding = null;
    }
    this.setWeekEndingModel();
    this.dateChange.emit(this.weekEndingModel);
  }

  public gotoNextWeek(){
    if (this.curtimeSheet && this.curtimeSheet.weekEnding) {
      const d = this.curtimeSheet.weekEnding;
      const day = d.getDay();
      if(day < 0 && day > 7){
        return;
      }
      if (day !== 6) {
        d.setDate(d.getDate() + (6 - day) + TIMESHEET_DISPLAY_PERIOD);
      }else {
        d.setDate(d.getDate() +  TIMESHEET_DISPLAY_PERIOD);
      }
      this.curtimeSheet.weekEnding = this.getSaturday(d);
    } else {
      this.curtimeSheet.weekEnding = null;
    }
    this.setWeekEndingModel();
    this.dateChange.emit(this.weekEndingModel);
  }


  public getSaturday(d:any): Date {
    //let ind:Date = moment(d).toDate();
    let retd:Date = DateService.getSaturday(d); // moment(d).day('Saturday').startOf('day').toDate();
    //console.log('tdlplan-sheet - saturday of ' + ind.toString() + ' is ' + retd.toString() ) ;
    return retd;
  }

  public onWeekEndingDateChanged(event: IMyDateModel) {
    if (event.jsdate) {
      this.curtimeSheet.weekEnding = this.getSaturday(event.jsdate);
    } else {
      this.curtimeSheet.weekEnding = null;
    }
    this.setWeekEndingModel();
    this.dateChange.emit(this.weekEndingModel);
  }

  public hasRole(rolename): boolean{
    return this.authService.jwt_hasRole("ROLE_" + rolename);
  }

  public saveTimeSheet(){
    jQuery('#tbl-tdlplansheet').parsley().validate();
    if (jQuery('#tbl-tdlplansheet').parsley().isValid()) {
      this.endEdit.emit(true);
    }
  }

  public onDeleteTimesheetResourceClick(timesheetResource: TDLPlanTimesheetResource) {
    const i = this.curtimeSheet.ptsresources.indexOf(timesheetResource);
    this.curtimeSheet.ptsresources.splice(i, 1);
  }

  public onAddTimesheetResourceClick() {
    const newsheetResource = new TDLPlanTimesheetResource();
    newsheetResource.hours = [0, 0, 0, 0, 0, 0, 0];
    this.curtimeSheet.ptsresources.push(newsheetResource);
  }

  public calcTotalHoursByTimesheetResource(timesheetResource: TDLPlanTimesheetResource): number {
    let result = 0;
    for (let i = 0; i < timesheetResource.hours.length; i++) {
      result += timesheetResource.hours[i];
    }
    return result;
  }


  public calcTotalCostByTimesheetResource(timesheetResource: TDLPlanTimesheetResource, rate: number): number {
    let result = 0;

    if(!isNil(timesheetResource)){
      for (let i = 0; i < timesheetResource.hours.length; i++) {
        result += timesheetResource.hours[i] * rate;
      }
    }
    return result;
  }


  public calcTotalHoursByDay(dayNumber: number): number {
    let result = 0;

    if(!isNil(this.curtimeSheet)){
      for (let i = 0; i < this.curtimeSheet.ptsresources.length; i++) {
        result += this.curtimeSheet.ptsresources[i].hours[dayNumber];
      }
    }
    return result;
  }

  public calcTotalHours(): number {
    let result = 0;

    if(!isNil(this.curtimeSheet)){
      for (let i = 0; i < this.curtimeSheet.ptsresources.length; i++) {
        result += this.calcTotalHoursByTimesheetResource(
          this.curtimeSheet.ptsresources[i]
        );
      }
    }

    return result;
  }

  public getBPARateForWeekEnding(weekEnding:Date, res:TDLPlanTimesheetResource){
    return 0.0;
  }

  public calcTotalCost(): number {
    let result = 0;

    if(!isNil(this.curtimeSheet)){
      for (let i = 0; i < this.curtimeSheet.ptsresources.length; i++) {
        result += this.calcTotalCostByTimesheetResource(
          this.curtimeSheet.ptsresources[i], this.getBPARateForWeekEnding(this.curtimeSheet.weekEnding, this.curtimeSheet.ptsresources[i])
        );
      }
    }

    return result;
  }

  public getFormattedDate(d:Date): string {
    return `${d.getFullYear()}-${d.getMonth() + 1}-${d.getDate()}`;
  }

  public displayWorkLoadGraph(selResource: TDLPlanTimesheetResource, weekEnding:any){
    this.ShowWorkLoadGraph = true;
    this.selResource = selResource;
    let query = new ResourceTDLQuery();
    query.resourceID = selResource.resourceID;
    query.weekEnding = this.getFormattedDate(weekEnding);
    this.selQuery = query;
  }

  public getDate(index: number){
    let startWeekEnding = DateService.getThisSaturday(); //moment().day('Saturday').toDate();
    if(!isNil(this.curtimeSheet) && !isNil(this.curtimeSheet.weekEnding)){
      startWeekEnding = DateService.resetTime(this.curtimeSheet.weekEnding);  // moment(this.curtimeSheet.weekEnding).toDate();
    }
    return DateService.getFormattedText(
      DateService.getDateByAddingDaysFromDate(
        DateService.getSaturday(startWeekEnding), index
      ), "MM/DD"
    );
    // return moment(startWeekEnding).day('Saturday').add(index, 'days').format("MM/DD");
  }
}
