import { Injectable } from '@angular/core';
import { Subject } from 'rxjs';
import { TimesheetDocModel } from '../models/tsdoc.model';
/**
 *
 *
 * @export
 * @class ActualTimesheetEventService
 */
@Injectable()
export class ActualTimesheetEventService {

  private fileIngestedSource = new Subject<TimesheetDocModel>();

  fileIngestedSourceLoaded$ = this.fileIngestedSource.asObservable();

  public triggerFileIngested(fileIngestedModelArry: TimesheetDocModel) {
    this.fileIngestedSource.next(fileIngestedModelArry);
  }

}

