import * as _ from 'lodash';
import { Pipe, PipeTransform } from '@angular/core';
import { WeeklyTDLResourcePlanFilterData } from '../models/wkltdl-resource-filter.model';

@Pipe({
    name: "wktdlfilterpipe"
})
export class WeeklyTDLDataFilterPipe implements PipeTransform {
    transform(data: any[], filter: WeeklyTDLResourcePlanFilterData): any {
        let results = data;

        if (data) {
            if (filter) {
                let handler = this;
                // filter by tdl name
                if (typeof filter.nameFilter !== 'undefined' && filter.nameFilter.length > 0) {
                    results =  _.filter(results, function(item) {
                        let val1:string = item.name;
                        let val2:string = filter.nameFilter;
                        return val1.toUpperCase().indexOf(val2.toUpperCase()) > -1;
                    });
                                                        
                }
            }
        }
        return results;
    }
// tslint:disable-next-line:eofline
}