import { AfterViewInit, Component, EventEmitter, Input, OnDestroy, OnInit, Output, SimpleChanges, ViewEncapsulation } from "@angular/core";
import { ActivatedRoute, Router } from "@angular/router";
import * as _ from "lodash";
import { isNil } from 'lodash';
import { Subject } from "rxjs";
import { AuthService } from '../../../../login/services/auth.service';
import { DateService } from '../../../../shared/service/date.service';
import { Resource } from '../../../../tdl/models/resource.model';
import { TDL } from '../../../../tdl/models/tdl.model';
import { TDLResourceView } from '../../../../tdl/models/tdlresourceview.model';
import { ResourceService } from '../../../../tdl/services/resource.service';
import { TDLService } from '../../../../tdl/services/tdl.service';
import { TDLResourceService } from '../../../../tdl/services/tdlresource.service';
import { TDLPlanHoursQuery } from '../../../models/planhr-query.model';
import { ResourceTDLQuery } from '../../../models/restdl-query.model';
import { WeeklyResourcePlanHours } from '../../../models/wkly-resplanhours.model';
import { WeeklyResourcePlanTimesheet, WeeklyResourcePlanTimesheetProject } from '../../../models/wkly-resplansheet.model';
import { PlanSheetService } from '../../../services/plan-sheet.service';
import { ResourceTDLPlanView } from './../../../models/res-tdl-planview.model';

declare var jQuery: any;

@Component({
  selector: '[wkly-resplanner]',
  templateUrl: "./wkly-resplanner.template.html",
  styleUrls: ["./wkly-resplanner.style.scss"],
  providers: [TDLService, ResourceService, TDLResourceService, PlanSheetService],
  encapsulation: ViewEncapsulation.None
})
export class ResourceWeeklyTimesheetPlannerComponent implements OnInit, AfterViewInit, OnDestroy {

  @Input() resourceid: number;
  @Input() resourcename: string;

  @Output() saveupdate: EventEmitter<boolean> = new EventEmitter();

  public tdlID: number;
  public resourcedata: any[];

  public selResourceID: number;
  public selParamResourceID: number;
  public selResourceName: string;

  public arrWeeklyPlanHours: WeeklyResourcePlanHours[] = [];
  public listResources:Resource[]=[];
  public listResourceTDLs:ResourceTDLPlanView[]=[];
  public resourcetdlbreakhours: TDLResourceView[]=[];
  public editTimesheet: WeeklyResourcePlanTimesheet;
  public isLoading:boolean = true;
  public isSaving:boolean = false;

  public busyloadQuery: any;
  public busyloadTDL: any;
  public busyloadTDLResource: any;
  public busyloadAllResources: any;
  public busyloadSingleResource: any;
  public busyloadResourceTDL: any;

  public parentSaveTimesheet:Subject<boolean> = new Subject();

  private tdlmodel: TDL;
  public showErrorFlag: boolean;
  public alerts: Array<Object>;

  constructor(
    private planSheetService: PlanSheetService,
    private resourceService: ResourceService,
    private tdlResourceService: TDLResourceService,
    private tdlService: TDLService,
    private router: Router,
    private route: ActivatedRoute,
    private authService: AuthService
  ) {
    this.alerts = [
      {
        type: "warning",
        msg:
          '<span class="fw-semi-bold">Warning:</span> Placeholder for Error Validation messages'
      }
    ];

    this.showErrorFlag = false;
    this.selParamResourceID = -1;
  }

  ngOnInit() {
    // console.log('ngOnInit - wkly-resplanner.component - started');
    this.selResourceID = 0;
    this.resInfoLoad();
    // console.log('ngOnInit - wkly-resplanner.component - stopped');
  }

  ngAfterViewInit() {
  }

  public hasRole(rolename): boolean{
    return this.authService.jwt_hasRole("ROLE_" + rolename);
  }

  public reset(flag){
    jQuery("#ts-resplanner").modal("hide");
    if(this.isSaving){
      this.saveupdate.emit(true);
      this.isSaving = false;
    }
  }


  ngOnChanges(changes: SimpleChanges) {
    for (let propName in changes) {
      if (propName === "resourceid") {
        let chg = changes[propName];
        this.selResourceID = chg.currentValue;
        this.selParamResourceID = this.selResourceID;
        this.loadResources();
      }
      if(propName  === 'resourcename'){
        let chg = changes[propName];
        this.selResourceName = chg.currentValue;
      }
    }
  }

  ngOnDestroy() {
    if(this.busyloadTDL){
      this.busyloadTDL.unsubscribe();
    }
    if(this.busyloadAllResources){
      this.busyloadAllResources.unsubscribe();
    }
    if(this.busyloadSingleResource){
      this.busyloadSingleResource.unsubscribe();
    }
    if(this.busyloadTDLResource){
      this.busyloadTDLResource.unsubscribe();
    }
    if(this.busyloadResourceTDL){
      this.busyloadResourceTDL.unsubscribe();
    }
    if (this.busyloadQuery) {
      this.busyloadQuery.unsubscribe();
    }
  }

  private getPlanHour(resourceID: number) {
    let rowIndex = _.findIndex(this.arrWeeklyPlanHours, function(o) {
      return o.tdlResourceID === resourceID;
    });
    if (rowIndex !== -1) {
      return this.arrWeeklyPlanHours[rowIndex];
    }
    return null;
  }

  public getThisSaturday(): Date {
    return DateService.getThisSaturday();
    // let retd:Date = moment().day('Saturday').startOf('day').toDate();
    // console.log('wkly-resplanner - this saturday ' + retd.toString() ) ;
    // return retd;
  }

  private reconstructTimeSheetForTDLFromPlanHours(wkEnding:Date, numWeekstoDisplay:number) {
    let newTimesheet = new WeeklyResourcePlanTimesheet();
    // using resources from DB
    newTimesheet.weekEnding = wkEnding;
    newTimesheet.numWeeks = numWeekstoDisplay;
    for (let t of this.listResourceTDLs) {
      let ptsr = new WeeklyResourcePlanTimesheetProject();
      ptsr.tdlResourceID = t.tdlResourceID;
      ptsr.resourceID = this.selResourceID;
      ptsr.tdlID = t.tdlID;
      ptsr.tdlName = t.tdlName;
      ptsr.wbsCode = t.wbsCode;
      ptsr.planWeekHours = t.planWeekHours;
      ptsr.allPM = t.allPM;
      ptsr.isApproved = (t.approved === 'Y') ? true: false;
      ptsr.weekhours = [0, 0, 0, 0, 0, 0, 0];
      ptsr.actweekhours = [0, 0, 0, 0, 0, 0, 0];
      let ph = this.getPlanHour(t.tdlResourceID);
      if (ph != null) {
        ptsr.weekhours = [ph.wk1, ph.wk2, ph.wk3, ph.wk4, ph.wk5, ph.wk6, ph.wk7];
        ptsr.actweekhours = [ph.awk1, ph.awk2, ph.awk3, ph.awk4, ph.awk5, ph.awk6, ph.awk7];
        ptsr.weekrates = [ph.wkr1, ph.wkr2, ph.wkr3, ph.wkr4, ph.wkr5, ph.wkr6, ph.wkr7];
      } else {
        ptsr.weekhours = [0, 0, 0, 0, 0, 0, 0];
        ptsr.actweekhours = [0, 0, 0, 0, 0, 0, 0];
        ptsr.weekrates = [0, 0, 0, 0, 0, 0, 0];
      }
      ptsr.balanceHours = 0.0;

      let periodplantotal = 0.0;
      let periodactualtotal = 0.0;
      for (let i = 0; i < numWeekstoDisplay; i++) {
        periodplantotal += ptsr.weekhours[i];
        periodactualtotal += ptsr.actweekhours[i];
      }

      ptsr.balanceHours = periodplantotal - periodactualtotal;

      if((periodplantotal > 0.0) || (periodactualtotal > 0.0)){
        newTimesheet.ptsprojects.push(ptsr);
      }
    }
    this.editTimesheet = newTimesheet;
  }

  private getFormattedDate(d:Date): string {
    return `${d.getFullYear()}-${d.getMonth() + 1}-${d.getDate()}`;
  }

  private getPerformancePeriodStartWeekEnd(selDate:Date){
    let startWeekEnding:Date = DateService.getFirstSaturdayOfMonthFromGivenDate(selDate);
    // let startWeekEnding = moment(selDate).startOf('month').day('Saturday').toDate();
    return startWeekEnding;
  }

  private getPerformancePeriodEndWeekEnd(selDate:Date){
    let endWeekEnding = DateService.getLastSaturdayOfMonthFromGivenDate(selDate); // moment(selDate).endOf('month').day('Saturday').toDate();
    if(DateService.getMonthOfDate(endWeekEnding) !== DateService.getMonthOfDate(selDate)){
      // endWeekEnding = moment(endWeekEnding).subtract(7, 'days').toDate();
      endWeekEnding = DateService.getDateBySubtractingDaysFromDate(endWeekEnding, 7);
    }
    return endWeekEnding;
    // let endWeekEnding = moment(selDate).endOf('month').day('Saturday').toDate();
    // if(moment(endWeekEnding).month() !== moment(selDate).month()){
    //   endWeekEnding = moment(endWeekEnding).subtract(7, 'days').toDate();
    // }
    // return endWeekEnding;
  }

  private getWeeklyPlanResourceHoursForWeekEnding(wkEnding:Date, resourceID:number){
    let query: TDLPlanHoursQuery = new TDLPlanHoursQuery();

    let startweekEnding = this.getPerformancePeriodStartWeekEnd(wkEnding);
    let endWeekEnding = this.getPerformancePeriodEndWeekEnd(wkEnding);
    let numWeekstoDisplay = DateService.getDifferenceBetweenDatesinWeeks(startweekEnding, endWeekEnding); // moment(endWeekEnding).diff(moment(startweekEnding), 'week') + 1;

    query.weekEnding = this.getFormattedDate(startweekEnding);

    query.resourceID = resourceID;
    let handler = this;

    // Query Timesheet from DB for the weekending
    this.planSheetService
      .getWeeklyPlanResourceHoursForWeekEnding(query)
      .subscribe(data => {
        if (data) {
          handler.arrWeeklyPlanHours = data;
          handler.reconstructTimeSheetForTDLFromPlanHours(wkEnding, numWeekstoDisplay);
        }
      },
      error => {
        this.showErrorFlag = true;
        this.alerts = [];
        this.alerts.push({ type: "warning", msg: error });
      }
    );
  }


  private reloadResourcePlanAndActualHours(wkEnding:Date, resourceID: number){
    let handler = this;
    this.busyloadSingleResource = this.tdlResourceService.getResourceTDLs(resourceID).subscribe(
      data => {
        if (data) {
          handler.resourcetdlbreakhours = data;
          handler.getWeeklyPlanResourceHoursForWeekEnding(wkEnding, resourceID);
        }
      },
      error => {
        this.showErrorFlag = true;
        this.alerts = [];
        this.alerts.push({ type: "warning", msg: error });
      }
    );
  }

  private createTimeSheetForResource(wkEnding: Date, resourceID: number) {
    let query: ResourceTDLQuery = new ResourceTDLQuery();
    query.resourceID = resourceID;

    let startweekEnding = this.getPerformancePeriodStartWeekEnd(wkEnding);
    let endWeekEnding = this.getPerformancePeriodEndWeekEnd(wkEnding);
    let numWeekstoDisplay = DateService.getDifferenceBetweenDatesinWeeks(startweekEnding, endWeekEnding); // moment(endWeekEnding).diff(moment(startweekEnding), 'week') + 1;

    query.startweekEnding = this.getFormattedDate(startweekEnding);
    query.endweekEnding = this.getFormattedDate(startweekEnding);

    let handler = this;

    this.busyloadResourceTDL = this.tdlResourceService.getResourceTDLPlanViewForGivenWeekEndingRange(query).subscribe(data => {
      if (data) {
        handler.listResourceTDLs = data;
        handler.reloadResourcePlanAndActualHours(wkEnding, resourceID);
      }
    },
      error => {
        this.showErrorFlag = true;
        this.alerts = [];
        this.alerts.push({ type: "warning", msg: error });
      }
    );
  }

  private getResourceIDFromEmployeeID(employeeid){
    let rowIndex = _.findIndex(this.listResources, function (o) { return (o.employeeID === employeeid)});
    if(rowIndex !== -1) {
        return this.listResources[rowIndex].resourceID;
    }
  }

  public resetError(){
    this.showErrorFlag = false;
    this.alerts = [];
  }

  public loadResources(){
    let handler = this;
    this.resetError();
    this.busyloadAllResources = this.resourceService.getAll().subscribe(
      data => {
        if (data) {
          handler.listResources = data;
            let employeeid = this.authService.jwt_getEmployeeId();
            handler.selResourceID= handler.getResourceIDFromEmployeeID(employeeid);
            if(isNil(handler.selResourceID)){
              this.showErrorFlag = true;
              this.alerts = [];
              this.alerts.push({ type: "warning", msg: "Error: Unable to match User Login to TDL Resource. Check Employee ID match" });
            } else{
              handler.loadResourceTDLs(handler.selResourceID, handler.getFormattedDate(this.getPerformancePeriodStartWeekEnd(handler.getThisSaturday())));
            }
        }
      },
      error => {
        this.showErrorFlag = true;
        this.alerts = [];
        this.alerts.push({ type: "warning", msg: error });
      }
    );
  }

  // Load TDL resources
  private loadResourceTDLs(resourceID: number, weekEnding: string) {
    let query: ResourceTDLQuery = new ResourceTDLQuery();
    query.resourceID = resourceID;
    query.weekEnding = weekEnding;

    let handler = this;

    this.busyloadResourceTDL = this.tdlResourceService.getResourceTDLPlanViewForGivenWeekEnding(query).subscribe(data => {
      if (data) {
        handler.listResourceTDLs = data;
        handler.createTimeSheetForResource(this.getPerformancePeriodStartWeekEnd(handler.getThisSaturday()), resourceID);
      }
    },
      error => {
        this.showErrorFlag = true;
        this.alerts = [];
        this.alerts.push({ type: "warning", msg: error });
      }
    );
  }

  public resInfoLoad() {
    this.loadResources();
  }

  // resource changed
  public ResourceChanged(resourceID:number){
    // let wkEnding:any = new Date(event.date.year, event.date.month -1, event.date.day);
    this.loadResourceTDLs(resourceID, this.getFormattedDate(this.getPerformancePeriodStartWeekEnd(this.getThisSaturday())));
  }

  // Updated saved record ID
  private updateSavedTimeSheet() {
    for (let ptsr of this.editTimesheet.ptsprojects) {
      let rowIndex = _.findIndex(this.arrWeeklyPlanHours, function(o) {
        return o.tdlID === ptsr.tdlID;
      });
      if (rowIndex !== -1) {
        // ptsr.planHoursID = this.arrWeeklyPlanHours[rowIndex].planHoursID;
      }
    }

    let timesheetCpy:WeeklyResourcePlanTimesheet = Object.assign({}, this.editTimesheet);
    this.editTimesheet = null;
    this.editTimesheet = Object.assign({}, timesheetCpy);
  }

  public isEditingTimesheet(timesheet: WeeklyResourcePlanTimesheet) {
    if (timesheet) {
      return timesheet === this.editTimesheet;
    } else {
      return !!this.editTimesheet;
    }
  }

  private onTimesheetDateChanged(event){
    let wkEnding:any = new Date(event.date.year, event.date.month -1, event.date.day);
    this.createTimeSheetForResource(wkEnding, this.selResourceID);
  }

  private onTimesheetBeginEdit(timesheet: WeeklyResourcePlanTimesheet) {
    this.editTimesheet = timesheet;
  }

  private updatePlanAndActualHours(resourceID: number){
    let handler = this;
    this.busyloadSingleResource = this.tdlResourceService.getResourceTDLs(resourceID).subscribe(
      data => {
        if (data) {
          handler.resourcetdlbreakhours = data;
          handler.parentSaveTimesheet.next(true);
        }
      },
      error => {
        this.showErrorFlag = true;
        this.alerts = [];
        this.alerts.push({ type: "warning", msg: error });
      }
    );
  }

  private onTimesheetEndEdit(timesheet: WeeklyResourcePlanTimesheet) {
    this.isSaving = true;
    let handler = this;
    timesheet.weekEnding = DateService.resetTime(timesheet.weekEnding); // moment(timesheet.weekEnding).startOf('day').toDate();
    this.planSheetService
      .saveWeeklyResourcePlanHours(timesheet, this.selResourceID)
      .subscribe(data => {
        if (data) {
          handler.arrWeeklyPlanHours = data;
          handler.updateSavedTimeSheet();
          handler.updatePlanAndActualHours(handler.selResourceID);
        }
      },
        error => {
          this.showErrorFlag = true;
          this.alerts = [];
          this.alerts.push({ type: "warning", msg: error });
        }
      );
  }
}
