import { CommonModule } from '@angular/common';
import { NgModule, CUSTOM_ELEMENTS_SCHEMA } from '@angular/core';
import { RouterModule } from '@angular/router';
import { CalendarModule, DateAdapter } from 'angular-calendar';
import { adapterFactory } from 'angular-calendar/date-adapters/date-fns';
import { ChartsModule } from 'ng2-charts';
import { AuthService } from '../../login/services/auth.service';
import { SharedModule } from '../../shared/shared.module';
import { ActualTimesheetEventService } from '../services/act-sheet-event.service';
import { ActualSheetService } from '../services/act-sheet.service';
import { PlanSheetService } from '../services/plan-sheet.service';
import { WeeklyTDLDataFilterPipe } from '../util/wk-tdl-filter.pipe';
import { ActualSheetComponent } from './actual/act-sheet.component';
import { TimesheetActualComponent } from './actual/ts-actual.component';
import { TimesheetIngestErrorPopup } from './ingest/at-ingest-error.popup.component';
import { TimesheetProgressBarDynamicComponent } from './ingest/at-progressbar-modal.component';
import { IngestTimesheetComponent } from './ingest/ingest-timesheet.component';
import { InvoiceComponent } from './invoice/invoice.component';
import { ResourceWorkLoadComponent } from './res-wkload.component';
import { ResourcePlanWeeklySheetComponent } from './resplan/weekly/resplan-wklysheet.component';
import { ResourceWeeklyTimesheetPlannerComponent } from './resplan/weekly/wkly-resplanner.component';
import { TDLPlanSheetComponent } from './tdlplan/daily/tdlplan-sheet.component';
import { TDLTimesheetPlannerComponent } from './tdlplan/daily/ts-tdlplanner.component';
import { TDLPlanWeeklySheetComponent } from './tdlplan/weekly/tdlplan-wklysheet.component';
import { TDLWeeklyTimesheetPlannerComponent } from './tdlplan/weekly/wkly-tdlplanner.component';
import { TDLPlanPeriodSheetComponent } from './tdlplan/period/tdlplan-periodsheet.component';
import { TDLPeriodTimesheetPlannerComponent } from './tdlplan/period/period-tdlplanner.component';
import { ResourceWorkLoadCalendarPopup } from './workload/wkload-calendar.component';

export const routes = [
    { path: 'track', component: TimesheetActualComponent },
    { path: 'track/:id', component: TimesheetActualComponent },
    { path: 'tdlplan/:id', component: TDLTimesheetPlannerComponent },
    { path: 'tdlweekplan/:id', component: TDLWeeklyTimesheetPlannerComponent },
    { path: 'tdlperiodplan/:id', component: TDLPeriodTimesheetPlannerComponent },
    { path: 'resplan', component: ResourceWeeklyTimesheetPlannerComponent },
    { path: 'invoice', component: InvoiceComponent }
];

@NgModule({
    imports: [CommonModule, SharedModule, ChartsModule, RouterModule.forChild(routes),
        CalendarModule.forRoot({
            provide: DateAdapter,
            useFactory: adapterFactory
          })
    ],
    declarations: [TDLPlanSheetComponent, TDLTimesheetPlannerComponent, ResourcePlanWeeklySheetComponent,
                TimesheetActualComponent, ActualSheetComponent,  ResourceWeeklyTimesheetPlannerComponent, InvoiceComponent,
                ResourceWorkLoadCalendarPopup, TDLWeeklyTimesheetPlannerComponent, TDLPlanWeeklySheetComponent, WeeklyTDLDataFilterPipe,
                TDLPeriodTimesheetPlannerComponent, TDLPlanPeriodSheetComponent,
        ResourceWorkLoadComponent, IngestTimesheetComponent, TimesheetProgressBarDynamicComponent, TimesheetIngestErrorPopup],
    providers: [AuthService, PlanSheetService, ActualSheetService, ActualTimesheetEventService]
})

export class TimeSheetModule {
    static routes = routes;
}
