import {
	AfterViewInit,
	Component,
	OnDestroy,
	OnInit,
	ViewEncapsulation
} from "@angular/core";
import { ActivatedRoute, Router } from "@angular/router";
import * as _ from "lodash";
import { LocalStorageService } from "ngx-webstorage";
import { isNil } from "ramda";
import { Subject } from "rxjs";
import { ShareDataService } from "../../../../core/sharedata.service";
import { TDL } from "../../../../tdl/models/tdl.model";
import { TDLResourceView } from "../../../../tdl/models/tdlresourceview.model";
import { TDLService } from "../../../../tdl/services/tdl.service";
import { TDLResourceBudgetPlanHours } from "../../../models/bugetplanhrs.model";
import { LaborRatePeriod } from "../../../models/labrate-period.model";
import { TDLPlanActualHrsCostInfo } from "../../../models/planacthrscost.model";
import { PlanHours } from "../../../models/planhours.model";
import { TDLPlanHoursQuery } from "../../../models/planhr-query.model";
import { TDLHoursApproveInfo } from "../../../models/tdlhourspprove.model";
import {
	TDLPlanTimesheet,
	TDLPlanTimesheetResource
} from "../../../models/tdlplan-sheet.model";
import { PlanSheetService } from "../../../services/plan-sheet.service";
import { DateService } from "../../../../shared/service/date.service";

@Component({
	selector: "ts-tdlplanner",
	templateUrl: "./ts-tdlplanner.template.html",
	styleUrls: ["./ts-tdlplanner.style.scss"],
	providers: [TDLService],
	encapsulation: ViewEncapsulation.None
})
export class TDLTimesheetPlannerComponent
	implements OnInit, AfterViewInit, OnDestroy {
	public tdlID: number;
	public title = "Timesheets";
	public laborRatePeriods: LaborRatePeriod[];
	public resourcedata: any[];
	public arrPlanHours: PlanHours[] = [];
	public resources: TDLResourceView[] = [];
	public editTimesheet: TDLPlanTimesheet;
	public isLoading = true;
	public isSaving = false;
	public busyloadQuery: any;
	public busyloadTDL: any;
	public busyloadTDLResource: any;
	public busyLoadTDLTotalHours: any;
	public busyResPlanHours: any;
	public busyloadLaborRatePeriods: any;
	public planactHrsCostInfo: TDLPlanActualHrsCostInfo;

	public showErrorFlag: boolean;
	public alerts: Array<Object>;

	public apprvInfo: TDLHoursApproveInfo = new TDLHoursApproveInfo();

	public parentSaveTimesheet: Subject<boolean> = new Subject();

	public tdlmodel: TDL;
	constructor(
		private planSheetService: PlanSheetService,
		private storage: LocalStorageService,
		private tdlService: TDLService,
		private router: Router,
		private route: ActivatedRoute,
		private shareData: ShareDataService
	) {
		this.alerts = [
			{
				type: "warning",
				msg:
					'<span class="fw-semi-bold">Warning:</span> Placeholder for Error Validation messages'
			}
		];

		this.showErrorFlag = false;
	}

	ngOnInit() {
		this.resInfoLoad();
	}

	ngAfterViewInit() {}

	ngOnDestroy() {
		if (this.busyloadTDL) {
			this.busyloadTDL.unsubscribe();
		}
		if (this.busyloadTDLResource) {
			this.busyloadTDLResource.unsubscribe();
		}
		if (this.busyloadQuery) {
			this.busyloadQuery.unsubscribe();
		}
		if (this.busyLoadTDLTotalHours) {
			this.busyLoadTDLTotalHours.unsubscribe();
		}
		if (this.busyResPlanHours) {
			this.busyResPlanHours.unsubscribe();
		}
		if (this.busyloadLaborRatePeriods) {
			this.busyloadLaborRatePeriods.unsubscribe();
		}
	}

	public getThisSaturday(): Date {
		return DateService.getThisSaturday();
		// let retd:Date = DateService.getThisSaturday(); // moment().day('Saturday').startOf('day').toDate();
		// // console.log('ts-tdlplanner - this saturday ' + retd.toString() ) ;
		// return retd;
	}

	public getResourceName(tdlResourceID: number) {
		let resourceName: string = "";
		let rowIndex = _.findIndex(this.resources, function(o) {
			return o.tdlResourceID === tdlResourceID;
		});
		if (rowIndex !== -1) {
			resourceName = this.resources[rowIndex].name;
		}
		return resourceName;
	}

	public getPlanHour(tdlResourceID: number) {
		let rowIndex = _.findIndex(this.arrPlanHours, function(o) {
			return o.tdlResourceID === tdlResourceID;
		});
		if (rowIndex !== -1) {
			return this.arrPlanHours[rowIndex];
		}
		return null;
	}

	public getResourceRateForWeekEnding(r: TDLResourceView, wkEnding: Date) {
		if (!isNil(this.laborRatePeriods)) {
			let bpacode = "base";
			for (let lr of this.laborRatePeriods) {
				let target = DateService.resetTime(wkEnding); //moment(wkEnding);
				let start = DateService.resetTime(lr.startDate); // moment(lr.startDate);
				let end = DateService.resetTime(lr.endDate); // moment(lr.endDate);
				// if(target.isBetween(start, end, 'days', '[]')){
				if (DateService.isDateBetweenDates(target, start, end)) {
					bpacode = lr.name;
					break;
				}
			}

			switch (bpacode) {
				case "base":
					return r.baseRate;

				case "op1":
					return r.op1Rate;

				case "op2":
					return r.op2Rate;

				case "op3":
					return r.op3Rate;

				case "op4":
					return r.op4Rate;

				case "op5":
					return r.op5Rate;

				default:
					break;
			}
		}
		return 0.0;
	}

	public reconstructTimeSheetForTDLFromPlanHours(wkEnding: Date) {
		let newTimesheet = new TDLPlanTimesheet();
		// using resources from DB
		let index = 0;
		newTimesheet.weekEnding = wkEnding;
		for (let r of this.resources) {
			let ptsr = new TDLPlanTimesheetResource();
			ptsr.tdlResourceID = r.tdlResourceID;
			ptsr.resourceID = r.resourceID;
			ptsr.name = r.name;
			ptsr.weekRate = this.getResourceRateForWeekEnding(r, wkEnding);
			ptsr.hours = [0, 0, 0, 0, 0, 0, 0];
			let ph = this.getPlanHour(r.tdlResourceID);
			if (ph != null) {
				ptsr.planHoursID = ph.planHoursID;
				ptsr.hours = [
					ph.sunHrs,
					ph.monHrs,
					ph.tueHrs,
					ph.wedHrs,
					ph.thuHrs,
					ph.friHrs,
					ph.satHrs
				];
				if (index === 0) {
					newTimesheet.approvalStatus = ph.approvalStatus;
					newTimesheet.approvedBy = ph.approvedBy;
					newTimesheet.approvedDt = ph.approvedDt;
				}
			} else {
				ptsr.hours = [0, 0, 0, 0, 0, 0, 0];
			}
			ptsr.balanceHours = r.budgetHours - r.planHours;
			newTimesheet.ptsresources.push(ptsr);
			index++;
		}
		this.editTimesheet = newTimesheet;
		// this.updatePlannedHoursForAllTDLResources(this.tdlID);
	}

	public getFormattedDate(d: Date): string {
		return `${d.getFullYear()}-${d.getMonth() + 1}-${d.getDate()}`;
	}

	public createTimeSheetForTDL(wkEnding: Date) {
		let query: TDLPlanHoursQuery = new TDLPlanHoursQuery();
		query.weekEnding = this.getFormattedDate(wkEnding);
		query.tdlID = this.tdlID;
		let handler = this;
		// Query Timesheet from DB for the weekending
		this.planSheetService.getPlanHoursForWeekEndingAndTDL(query).subscribe(
			data => {
				if (data) {
					handler.arrPlanHours = data;
					handler.reconstructTimeSheetForTDLFromPlanHours(wkEnding);
				}
			},
			error => {
				this.showErrorFlag = true;
				this.alerts = [];
				this.alerts.push({ type: "warning", msg: error });
			}
		);
	}

	public loadTDLInfo(tdlID: number) {
		this.busyloadTDL = this.tdlService.getTDL(tdlID).subscribe(data => {
			if (data) {
				this.tdlmodel = data;
			}
		});
	}

	// Load Labor Rate Periods
	public loadLaborRatePeriods(weekEnding: Date) {
		let handler = this;
		this.busyloadLaborRatePeriods = this.tdlService
			.getLaborRatePeriodList()
			.subscribe(data => {
				if (data) {
					handler.laborRatePeriods = data;
					// Create Time Sheet (either saved or new)
					handler.createTimeSheetForTDL(weekEnding);
				}
			});
	}

	// Load TDL resources
	public reloadTimesheet(tdlID: number, weekEnding: Date) {
		this.busyloadTDLResource = this.tdlService
			.getTDLResources(tdlID)
			.subscribe(
				data => {
					if (data) {
						this.resources = data;
						// Create Time Sheet (either saved or new)
						this.loadLaborRatePeriods(weekEnding);
					}
				},
				error => {
					this.showErrorFlag = true;
					this.alerts = [];
					this.alerts.push({ type: "warning", msg: error });
				}
			);
	}

	// Load TDL resources
	public loadTDLResources(tdlID: number) {
		this.busyloadTDLResource = this.tdlService
			.getTDLResources(tdlID)
			.subscribe(
				data => {
					if (data) {
						this.resources = data;
						// Create Time Sheet (either saved or new)
						this.loadLaborRatePeriods(this.getThisSaturday());
					}
				},
				error => {
					this.showErrorFlag = true;
					this.alerts = [];
					this.alerts.push({ type: "warning", msg: error });
				}
			);
	}

	public updateTDLTotalHoursAndCost(tdlID) {
		this.busyLoadTDLTotalHours = this.planSheetService
			.getTotalPlanHoursAndCostForTDL(tdlID)
			.subscribe(
				data => {
					if (data) {
						this.planactHrsCostInfo = data;
					}
				},
				error => {
					this.showErrorFlag = true;
					this.alerts = [];
					this.alerts.push({ type: "warning", msg: error });
				}
			);
	}

	public updateEditTimeSheetResourceBudgetPlanHours(
		data: TDLResourceBudgetPlanHours,
		resourceID: number
	) {
		let rowIndex = _.findIndex(this.editTimesheet.ptsresources, function(o) {
			return o.resourceID === resourceID;
		});
		if (rowIndex !== -1) {
			// this.editTimesheet.ptsresources[rowIndex].budgetedHours = data.budgetedHours;
			// this.editTimesheet.ptsresources[rowIndex].plannedHours = data.plannedHours;
		}
	}

	public updateResourcePlannedHours(
		ptsr: TDLPlanTimesheetResource,
		tdlID: number
	) {
		let handler = this;
		this.busyResPlanHours = this.planSheetService
			.getResourcePlannedHoursForTDL(ptsr.resourceID, tdlID)
			.subscribe(
				data => {
					if (data) {
						// ptsr.planhrs = data[0];
						handler.updateEditTimeSheetResourceBudgetPlanHours(
							data[0],
							ptsr.resourceID
						);
					}
				},
				error => {
					this.showErrorFlag = true;
					this.alerts = [];
					this.alerts.push({ type: "warning", msg: error });
				}
			);
	}

	public updatePlannedHoursForAllTDLResources(tdlID: number) {
		for (let ptsr of this.editTimesheet.ptsresources) {
			this.updateResourcePlannedHours(ptsr, tdlID);
		}
	}

	public resInfoLoad() {
		this.busyloadQuery = this.route.params.subscribe(
			params => {
				if (params["id"]) {
					this.tdlID = params["id"];
					// Load TDL resources
					this.loadTDLInfo(this.tdlID);
					this.loadTDLResources(this.tdlID);
					this.updateTDLTotalHoursAndCost(this.tdlID);
				}
			},
			error => {
				this.showErrorFlag = true;
				this.alerts = [];
				this.alerts.push({ type: "warning", msg: error });
			}
		);
	}

	// Updated saved record ID
	public updateSavedTimeSheet() {
		for (let ptsr of this.editTimesheet.ptsresources) {
			let rowIndex = _.findIndex(this.arrPlanHours, function(o) {
				return o.tdlResourceID === ptsr.tdlResourceID;
			});
			if (rowIndex !== -1) {
				ptsr.planHoursID = this.arrPlanHours[rowIndex].planHoursID;
			}
		}
	}

	public isEditingTimesheet(timesheet: TDLPlanTimesheet) {
		if (timesheet) {
			return timesheet === this.editTimesheet;
		} else {
			return !!this.editTimesheet;
		}
	}

	public onTimesheetDateChanged(event) {
		let wkEnding: any = new Date(
			event.date.year,
			event.date.month - 1,
			event.date.day
		);
		this.createTimeSheetForTDL(wkEnding);
	}

	public onTimesheetBeginEdit(timesheet: TDLPlanTimesheet) {
		this.editTimesheet = timesheet;
	}

	public getLoggedUser() {
		let loggedUser = this.shareData.loggedUser;
		if (loggedUser === undefined) {
			loggedUser = this.storage.retrieve("LoggedUser");
		}
		return loggedUser;
	}

	public onTimesheetApprove(timesheet: TDLPlanTimesheet, approved: boolean) {
		let handler = this;
		this.apprvInfo.tdlID = this.tdlID;
		this.apprvInfo.approvalStatus = approved ? "APRV" : "UNAP";
		this.apprvInfo.approvedBy = this.getLoggedUser();
		this.apprvInfo.approvalDt = new Date();
		this.apprvInfo.weekEnding = DateService.getFormattedText(
			DateService.resetTime(timesheet.weekEnding),
			"YYYY-MM-DD"
		); // moment(timesheet.weekEnding).format("YYYY-MM-DD");
		this.planSheetService
			.approvePlanHoursForWeekEndingAndTDL(this.tdlID, this.apprvInfo)
			.subscribe(
				data => {
					handler.arrPlanHours = data;
					handler.updateSavedTimeSheet();
					handler.updateTDLTotalHoursAndCost(handler.tdlID);
					handler.parentSaveTimesheet.next(true);
					handler.reloadTimesheet(
						handler.tdlID,
						handler.editTimesheet.weekEnding
					);
				},
				error => {
					this.showErrorFlag = true;
					this.alerts = [];
					this.alerts.push({ type: "warning", msg: error });
				}
			);
	}

	public onTimesheetEndEdit(timesheet: TDLPlanTimesheet, save: boolean) {
		if (save) {
			this.isSaving = true;
			let handler = this;
			timesheet.weekEnding = DateService.resetTime(timesheet.weekEnding); ////moment(timesheet.weekEnding).startOf("day").toDate();
			this.planSheetService
				.saveTDLPlanHours(timesheet, this.tdlID)
				.subscribe(
					data => {
						if (data) {
							handler.arrPlanHours = data;
							handler.updateSavedTimeSheet();
							handler.updateTDLTotalHoursAndCost(handler.tdlID);
							handler.parentSaveTimesheet.next(true);
							handler.reloadTimesheet(
								handler.tdlID,
								handler.editTimesheet.weekEnding
							);
						}
					},
					error => {
						handler.parentSaveTimesheet.next(false);
					}
				);
		}
	}
}
