import { Component, EventEmitter, Input, OnInit, Output, SimpleChanges, ViewEncapsulation } from '@angular/core';
import { TDLResourceView } from '../../../tdl/models/tdlresourceview.model';
import * as _ from 'lodash';
import { IMyDateModel, IMyDpOptions } from 'mydatepicker';
import { AlertComponent } from 'ngx-bootstrap';
import { isNil } from 'ramda';
import { Subscription, Subject } from 'rxjs';
import { AuthService } from '../../../login/services/auth.service';
import { ActualTimesheet, ActualTimesheetProject } from '../../models/act-sheet.model';
import { PoP } from '../../../tdl/models/pop.model';
import { TDLService } from '../../../tdl/services/tdl.service';
import { SendEmailQuery } from '../../models/emailquery.model';
import { ActualSheetService } from '../../services/act-sheet.service';
import { DateService } from '../../../shared/service/date.service';

declare var jQuery: any;

const TIMESHEET_DISPLAY_PERIOD = 7; // 1 WEEK - 7 DAYS

@Component({
  selector: "act-sheet",
  templateUrl: "./act-sheet.template.html",
  styleUrls: ["./act-sheet.style.scss"],
  encapsulation: ViewEncapsulation.None,
  providers: [TDLService]
})
export class ActualSheetComponent implements OnInit {
  public curtimeSheet: ActualTimesheet;

  public showErrorFlag: boolean;
  public showPlannedItemsOnly: boolean;
  public alerts: Array<Object>;

  public busy: Subscription;
  public busyLoadPops: Subscription;
  public busySendEmail: Subscription;

  public showwbsCode: boolean;
  public listPoPs: PoP[];

  public isReadonly: boolean;

  public selpopCode: string ="T01";

  myDatePickerOptions: IMyDpOptions = {
    dateFormat: 'mm/dd/yyyy',
    firstDayOfWeek: 'su',
    sunHighlight: false,
    satHighlight: true,
    showClearDateBtn: false
} ;

  @Input() timesheet: ActualTimesheet;
  @Input() canEdit: boolean;
  @Input() isEditing: boolean;
  @Input() resourceid: number;
  @Input() tdlbreakhours:TDLResourceView[];
  @Input() parentSaveTimesheet:Subject<boolean>;
  @Output() beginEdit = new EventEmitter();
  @Output() endEdit: EventEmitter<any> = new EventEmitter();
  @Output() delete = new EventEmitter();
  @Output() dateChange: EventEmitter<any> = new EventEmitter();

  public showAlertFlag: boolean;
  public resourcetdlbreakhours: TDLResourceView[]=[];
  public weekEndingModel: any = {};

  constructor(private authService: AuthService,
    private actualSheetService: ActualSheetService,
		private tdlService: TDLService) {
    this.alerts = [];
    this.showwbsCode = false;
    this.showPlannedItemsOnly = true;
    this.isReadonly = false;
    this.alerts = [
      {
        type: 'warning',
        msg: '<span class="fw-semi-bold">Warning:</span> Placeholder for Error Validation messages'
      }
    ];
    this.showErrorFlag = false;
  }

  ngOnInit() {
    this.busy = this.parentSaveTimesheet.subscribe(event => {
      if(event){
        this.addSuccess();
      }
    });
    this.loadPoPs();
  }

  ngOnDestroy(): void {
    if(this.busy){
      this.busy.unsubscribe();
    }
    if(this.busyLoadPops){
      this.busyLoadPops.unsubscribe();
    }
    if(this.busySendEmail){
      this.busySendEmail.unsubscribe();
    }
  }


	public loadPoPs() {
    let handler = this;
    this.busyLoadPops = this.tdlService.getPoPs().subscribe(
      data => {
        if (data) {
          handler.listPoPs = data;
        }
      },
      error => {
        this.showErrorFlag = true;
        this.alerts = [];
        this.alerts.push({ type: "warning", msg: error });
      }
    );
    }

    public setWeekEndingModel() {
    if (this.curtimeSheet.weekEnding) {
      this.weekEndingModel = {
        date: {
          year: this.curtimeSheet.weekEnding.getFullYear(),
          month: this.curtimeSheet.weekEnding.getMonth() + 1,
          day: this.curtimeSheet.weekEnding.getDate()
        }
      };
    }
  }

  public getBalanceHours(tdlID:number){
    let rowIndex = _.findIndex(this.resourcetdlbreakhours, function(o) {
      return o.tdlID === tdlID;
    });
    if (rowIndex !== -1) {
      let balancehours = this.resourcetdlbreakhours[rowIndex].planHours - this.resourcetdlbreakhours[rowIndex].actualHours;
      return balancehours;
    }
    return 0.0;
  }

  public isAllowedToDisplay(r:ActualTimesheetProject){
    if(this.showPlannedItemsOnly){
      if(r.planWeekHours === 0.0){
        return false;
      }
    }
    return true;
  }

  ngOnChanges(changes: SimpleChanges) {
    for (let propName in changes) {
      if(propName === "timesheet"){
        let chg = changes[propName];
        if (chg.currentValue) {
          this.curtimeSheet = chg.currentValue;
          if(this.showPlannedItemsOnly){
            // filter items that have zero planned hours
            for (let i = 0; i < this.curtimeSheet.ptsprojects.length; i++) {
              for(let day = 0; day < 7; day++){
                if(isNil(this.curtimeSheet.ptsprojects[i].planWeekHours)){
                  this.curtimeSheet.ptsprojects[i].planWeekHours = 0;
                }
              }
            }
          }
          this.setWeekEndingModel();
        }
      }
      if(propName === "tdlbreakhours"){
        let chg = changes[propName];
        if (chg.currentValue) {
          this.resourcetdlbreakhours = chg.currentValue;
        }
      }
    }
  }

  public addSuccess(): void {
    this.alerts.push({
      type: 'info',
      msg: `Timesheet Successfully Saved (added: ${new Date().toLocaleTimeString()})`,
      timeout: 5000
    });
  }

  public onClosed(dismissedAlert: AlertComponent): void {
    this.alerts = this.alerts.filter(alert => alert !== dismissedAlert);
  }

  public openSplitEditor(){
    jQuery('#splitbtn').click();
  }

  public isNegativeBalance(balance){
    if(balance < 0){
      return true;
    }
    return false;
  }

  public splitHours(hours:number, r:any){
    let dailyhrs = hours/5;
    r.hours[1] = dailyhrs;
    r.hours[2] = dailyhrs;
    r.hours[3] = dailyhrs;
    r.hours[4] = dailyhrs;
    r.hours[5] = dailyhrs;
  }

  public saveTimeSheet(){
    jQuery('#tbl-actsheet').parsley().validate();
    if (jQuery('#tbl-actsheet').parsley().isValid()) {
      // reset null values to 0
      for (let i = 0; i < this.curtimeSheet.ptsprojects.length; i++) {
        for(let day = 0; day < 7; day++){
          if(isNil(this.curtimeSheet.ptsprojects[i].hours[day])){
            this.curtimeSheet.ptsprojects[i].hours[day] = 0;
          }
        }
      }
      this.endEdit.emit(this.curtimeSheet);
    }
  }

  public hasRole(rolename): boolean{
    return this.authService.jwt_hasRole("ROLE_" + rolename);
  }

  public getFormattedDate(): string {
    if (this.curtimeSheet.weekEnding) {
      const we = new Date(this.curtimeSheet.weekEnding);
      return `${we.getMonth() + 1}/${we.getDate()}/${we.getFullYear()}`;
    } else {
      return null;
    }
  }

  /**
   * This check is used to make time entry fields readonly
   * However - PMO members can still edit time entries
   */
  public isOlderThanWeekNum(weeknum){
    if(!this.hasRole('PMO')){
      if (this.curtimeSheet && this.curtimeSheet.weekEnding) {
        let numWeekstoDisplay = DateService.getDifferenceBetweenDatesinWeeks(this.curtimeSheet.weekEnding, DateService.getThisSaturday()); // moment().day('Saturday').diff(moment(this.curtimeSheet.weekEnding), 'week') + 1;
        if(numWeekstoDisplay > weeknum) {
          return true;
        }
      }
    }
    return false;
  }

  public gotoPrevWeek(){
    if (this.curtimeSheet && this.curtimeSheet.weekEnding) {
      const d = this.curtimeSheet.weekEnding;
      const day = d.getDay();
      if(day < 0 && day > 7){
        return;
      }
      if (day !== 6) {
        d.setDate(d.getDate() + (6 - day) - TIMESHEET_DISPLAY_PERIOD);
      }else{
        d.setDate(d.getDate() - TIMESHEET_DISPLAY_PERIOD);
      }
      this.curtimeSheet.weekEnding = this.getSaturday(d);
    } else {
      this.curtimeSheet.weekEnding = null;
    }
    this.setWeekEndingModel();
    this.dateChange.emit(this.weekEndingModel);
  }

  public gotoNextWeek(){
    if (this.curtimeSheet && this.curtimeSheet.weekEnding) {
      const d = this.curtimeSheet.weekEnding;
      const day = d.getDay();
      if(day < 0 && day > 7){
        return;
      }
      if (day !== 6) {
        d.setDate(d.getDate() + (6 - day) + TIMESHEET_DISPLAY_PERIOD);
      }else {
        d.setDate(d.getDate() +  TIMESHEET_DISPLAY_PERIOD);
      }
      this.curtimeSheet.weekEnding = this.getSaturday(d);
    } else {
      this.curtimeSheet.weekEnding = null;
    }
    this.setWeekEndingModel();
    this.dateChange.emit(this.weekEndingModel);
  }

  public getSaturday(d:any): Date {
    return DateService.getSaturday(d);
    // let ind:Date = moment(d).toDate();
    // let retd:Date = moment(d).day('Saturday').startOf('day').toDate();
    // console.log('act-sheet - saturday of ' + ind.toString() + ' is ' + retd.toString() ) ;
    // return retd;
  }

  public onWeekEndingDateChanged(event: IMyDateModel) {
    if (event.jsdate) {
      this.curtimeSheet.weekEnding = this.getSaturday(event.jsdate);
    } else {
      this.curtimeSheet.weekEnding = null;
    }
    this.setWeekEndingModel();
    this.dateChange.emit(this.weekEndingModel);
  }

  public calcTotalHoursByTimesheetResource(timesheetResource: ActualTimesheetProject): number {
    let result = 0;
    for (let i = 0; i < timesheetResource.hours.length; i++) {
      result += isNil(timesheetResource.hours[i]) ? 0 : timesheetResource.hours[i];
    }
    return result;
  }

  public calcTotalHoursByDay(dayNumber: number): number {
    let result = 0;
    for (let i = 0; i < this.curtimeSheet.ptsprojects.length; i++) {
      result += isNil(this.curtimeSheet.ptsprojects[i].hours[dayNumber]) ? 0 : this.curtimeSheet.ptsprojects[i].hours[dayNumber];
    }
    return result;
  }

  public calcTotalHours(): number {
    let result = 0;
    for (let i = 0; i < this.curtimeSheet.ptsprojects.length; i++) {
      result += this.calcTotalHoursByTimesheetResource(
        this.curtimeSheet.ptsprojects[i]
      );
    }
    return result;
  }

  public getDate(index: number){
  //   let startWeekEnding = moment().day('Saturday').toDate();
  //   if(!isNil(this.curtimeSheet) && !isNil(this.curtimeSheet.weekEnding)){
  //     startWeekEnding = moment(this.curtimeSheet.weekEnding).toDate();
  //   }
  //   return moment(startWeekEnding).day('Saturday').add(index, 'days').format("MM/DD");

    let startWeekEnding = DateService.getThisSaturday(); //moment().day('Saturday').toDate();
    if(!isNil(this.curtimeSheet) && !isNil(this.curtimeSheet.weekEnding)){
      startWeekEnding = DateService.resetTime(this.curtimeSheet.weekEnding);  // moment(this.curtimeSheet.weekEnding).toDate();
    }
    return DateService.getFormattedText(
      DateService.getDateByAddingDaysFromDate(
        DateService.getSaturday(startWeekEnding), index
      ), "MM/DD"
    );
  }

  public getChargeAuthorizationTimeRange(){
    let dateStart = this.getDate(0);
    let dateEnd = this.getDate(6);
    return dateStart + " to " + dateEnd;
  }

  public emailTimeSheet(){
    let query: SendEmailQuery = new SendEmailQuery();
    query.resourceID = this.resourceid;
    query.timeRange = this.getChargeAuthorizationTimeRange();
    this.curtimeSheet.dayNames = [];
    for(let k = 0; k < 7; k++){
      this.curtimeSheet.dayNames[k] = this.getDate(k);
    }
    query.curTimeSheet = this.curtimeSheet;
    let handler = this;
    this.busySendEmail = this.actualSheetService.sendTimeSheetEmail(query).subscribe(
      data => {
        if (data) {
          this.alerts = [];
          this.showAlertFlag = true;
          this.alerts.push({
            type: 'info',
            msg: data.value + `(added: ${new Date().toLocaleTimeString()})`,
            timeout: 5000
          });
        }
      },
      error => {
        this.alerts = [];
        this.showAlertFlag = true;
        this.alerts.push({
          type: 'info',
          msg: 'Error: sending email' + `(added: ${new Date().toLocaleTimeString()})`,
          timeout: 5000
        });
      });
  }
}

