import { isNil } from 'ramda';
import { TDLResourceView } from '../../../tdl/models/tdlresourceview.model';

import {
  Component,
  OnInit,
  ViewEncapsulation,
  AfterViewInit,
  OnDestroy
} from "@angular/core";
import * as _ from "lodash";
import { Subscription, Subject } from "rxjs";
import { ActivatedRoute, Router } from "@angular/router";
import { ActualSheetService } from "../../services/act-sheet.service";
import { TDLService } from '../../../tdl/services/tdl.service';
import { ResourceService } from '../../../tdl/services/resource.service';
import { TDLResourceService } from '../../../tdl/services/tdlresource.service';
import { ActualHours } from '../../models/acthours.model';
import { Resource } from '../../../tdl/models/resource.model';
import { ActualTimesheet, ActualTimesheetProject } from '../../models/act-sheet.model';
import { TDL } from '../../../tdl/models/tdl.model';
import { AuthService } from '../../../login/services/auth.service';
import { ActualHoursQuery } from '../../models/acthr-query.model';
import { ResourceTDLQuery } from '../../models/restdl-query.model';
import { ResourceTDLPlanView } from '../../models/res-tdl-planview.model';
import { DateService } from '../../../shared/service/date.service';

@Component({
  selector: "ts-actual",
  templateUrl: "./ts-actual.template.html",
  styleUrls: ["./ts-actual.style.scss"],
  providers: [TDLService, ResourceService, TDLResourceService],
  encapsulation: ViewEncapsulation.None
})
export class TimesheetActualComponent implements OnInit, AfterViewInit, OnDestroy {
  public tdlID: number;
  public title = "Timesheets";
  public resourcedata: any[];
  public selResourceID: number;
  public arrActualHours: ActualHours[] = [];
  public listResources:Resource[]=[];
  public listResourceTDLs:ResourceTDLPlanView[]=[];
  public resourcetdlbreakhours: TDLResourceView[]=[];
  public editTimesheet: ActualTimesheet;
  public isLoading = true;
  public isSaving = false;
  public selpopCode: string = "T01";

  public busyloadQuery: any;
  public busyloadTDL: any;
  public busyloadTDLResource: any;
  public busyloadAllResources: any;
  public busyloadSingleResource: any;
  public busyloadResourceTDL: any;

  public parentSaveTimesheet:Subject<boolean> = new Subject();

  private tdlmodel: TDL;
  public showErrorFlag: boolean;
  public alerts: Array<Object>;

  constructor(
    private actSheetService: ActualSheetService,
    private resourceService: ResourceService,
    private tdlResourceService: TDLResourceService,
    private tdlService: TDLService,
    private router: Router,
    private route: ActivatedRoute,
    private authService: AuthService
  ) {
    this.alerts = [
      {
        type: "warning",
        msg:
          '<span class="fw-semi-bold">Warning:</span> Placeholder for Error Validation messages'
      }
    ];

    this.showErrorFlag = false;
  }

  ngOnInit() {
    this.selResourceID = 0;
    this.resInfoLoad();
  }

  ngAfterViewInit() {
  }

  public hasRole(rolename): boolean{
    return this.authService.jwt_hasRole("ROLE_" + rolename);
  }

  private resetError(flag: boolean){
    this.showErrorFlag = flag;
    this.alerts = [];
  }

  ngOnDestroy() {
    // console.log("ngOnDestroy: ts-actual started");
    this.resetError(false);

    if(this.busyloadTDL){
      this.busyloadTDL.unsubscribe();
    }
    if(this.busyloadAllResources){
      this.busyloadAllResources.unsubscribe();
    }
    if(this.busyloadSingleResource){
      this.busyloadSingleResource.unsubscribe();
    }
    if(this.busyloadTDLResource){
      this.busyloadTDLResource.unsubscribe();
    }
    if(this.busyloadResourceTDL){
      this.busyloadResourceTDL.unsubscribe();
    }
    if (this.busyloadQuery) {
      this.busyloadQuery.unsubscribe();
    }
    // console.log("ngOnDestroy: ts-actual completed");
  }

  public getThisSaturday(): Date {
    return DateService.getThisSaturday();
    // let retd:Date = moment().day('Saturday').startOf('day').toDate();
    // console.log('ts-actual - this saturday ' + retd.toString() ) ;
    // return retd;
  }

  private getActualHour(resourceID: number) {
    let rowIndex = _.findIndex(this.arrActualHours, function(o) {
      return o.tdlResourceID === resourceID;
    });
    if (rowIndex !== -1) {
      return this.arrActualHours[rowIndex];
    }
    return null;
  }

  private reconstructTimeSheetForTDLFromActualHours(wkEnding:Date) {
    let newTimesheet = new ActualTimesheet();
    // using resources from DB
    newTimesheet.weekEnding = wkEnding;
    for (let t of this.listResourceTDLs) {
      let ptsr = new ActualTimesheetProject();
      ptsr.tdlResourceID = t.tdlResourceID;
      ptsr.resourceID = this.selResourceID;
      ptsr.tdlID = t.tdlID;
      ptsr.tdlName = t.tdlName;
      ptsr.wbsCode = t.wbsCode;
      ptsr.planWeekHours = t.planWeekHours;
      ptsr.hours = [0, 0, 0, 0, 0, 0, 0];
      ptsr.weekRate = t.weekRate;
      let ah = this.getActualHour(t.tdlResourceID);
      if (ah != null) {
        ptsr.actualHoursID = ah.actualHoursID;
        ptsr.hours = [ah.sunHrs, ah.monHrs, ah.tueHrs, ah.wedHrs, ah.thuHrs, ah.friHrs, ah.satHrs];
      } else {
        ptsr.hours = [0, 0, 0, 0, 0, 0, 0];
      }
      ptsr.balanceHours = 0.0;
      if(t.approved === 'Y'){
        newTimesheet.ptsprojects.push(ptsr);
      }
    }
    this.editTimesheet = newTimesheet;
  }

  private getFormattedDate(d:Date): string {
    return `${d.getFullYear()}-${d.getMonth() + 1}-${d.getDate()}`;
  }


  private getActualResourceHoursForWeekEnding(wkEnding:Date, resourceID:number){
    let query: ActualHoursQuery = new ActualHoursQuery();
    query.weekEnding = this.getFormattedDate(wkEnding);
    query.resourceID = resourceID;
    let handler = this;
    // Query Timesheet from DB for the weekending
    this.actSheetService
      .getActualResourceHoursForWeekEnding(query)
      .subscribe(data => {
        if (data) {
          handler.arrActualHours = data;
          handler.reconstructTimeSheetForTDLFromActualHours(wkEnding);
        }
      },
      error => {
        handler.resetError(true);
        handler.alerts.push({ type: "warning", msg: isNil(error) ? "Error: Unable to get actual hours for weekending" : error });
      });
  }


  private reloadResourcePlanAndActualHours(wkEnding:Date, resourceID: number){
    let handler = this;
    this.busyloadSingleResource = this.tdlResourceService.getResourceTDLs(resourceID).subscribe(
      data => {
        if (data) {
          handler.resourcetdlbreakhours = data;
          handler.getActualResourceHoursForWeekEnding(wkEnding, resourceID);
        }
      },
      error => {
        this.showErrorFlag = true;
        this.alerts = [];
        this.alerts.push({ type: "warning", msg: error });
      }
    );
  }

  private createTimeSheetForResource(wkEnding: Date, resourceID: number) {
    let query: ResourceTDLQuery = new ResourceTDLQuery();
    query.resourceID = resourceID;
    query.weekEnding = this.getFormattedDate(wkEnding);
    query.popCode = this.selpopCode;

    let handler = this;

    this.busyloadResourceTDL = this.tdlResourceService.getResourceTDLPlanViewForGivenWeekEnding(query).subscribe(data => {
      if (data) {
        handler.listResourceTDLs = data;
        handler.reloadResourcePlanAndActualHours(wkEnding, resourceID);
      }
    },
      error => {
        this.showErrorFlag = true;
        this.alerts = [];
        this.alerts.push({ type: "warning", msg: error });
      }
    );
  }

  private getResourceIDFromEmployeeID(employeeid){
    let rowIndex = _.findIndex(this.listResources, function (o) { return (o.employeeID === employeeid)});
    if(rowIndex !== -1) {
        return this.listResources[rowIndex].resourceID;
    }
  }

  public loadResources(){
    this.busyloadAllResources = this.resourceService.getAll().subscribe(
      data => {
        if (data) {
          this.listResources = data;
          if(this.hasRole("USER") || this.hasRole("PM")){
            let employeeid = this.authService.jwt_getEmployeeId();
            this.selResourceID= this.getResourceIDFromEmployeeID(employeeid);
            this.loadResourceTDLs(this.selResourceID, this.getFormattedDate(this.getThisSaturday()));
          }
        }
      },
      error => {
        this.showErrorFlag = true;
        this.alerts = [];
        this.alerts.push({ type: "warning", msg: error });
      }
    );
  }

  // Load TDL resources
  private loadResourceTDLs(resourceID: number, weekEnding: string) {
    let query: ResourceTDLQuery = new ResourceTDLQuery();
    query.resourceID = resourceID;
    query.weekEnding = weekEnding;
    query.popCode = this.selpopCode;

    let handler = this;

    this.busyloadResourceTDL = this.tdlResourceService.getResourceTDLPlanViewForGivenWeekEnding(query).subscribe(data => {
      if (data) {
        handler.listResourceTDLs = data;
        handler.createTimeSheetForResource(this.getThisSaturday(), resourceID);
      }
    },
      error => {
        this.showErrorFlag = true;
        this.alerts = [];
        this.alerts.push({ type: "warning", msg: error });
      }
    );
  }

  public resInfoLoad() {
    this.loadResources();
  }

  // resource changed
  public ResourceChanged(resourceID:number){
    // let wkEnding:any = new Date(event.date.year, event.date.month -1, event.date.day);
    this.loadResourceTDLs(resourceID, this.getFormattedDate(this.getThisSaturday()));
  }

  // Updated saved record ID
  private updateSavedTimeSheet() {
    for (let ptsr of this.editTimesheet.ptsprojects) {
      let rowIndex = _.findIndex(this.arrActualHours, function(o) {
        return o.tdlID === ptsr.tdlID;
      });
      if (rowIndex !== -1) {
        ptsr.actualHoursID = this.arrActualHours[rowIndex].actualHoursID;
      }
    }

    let timesheetCpy:ActualTimesheet = Object.assign({}, this.editTimesheet);
    this.editTimesheet = null;
    this.editTimesheet = Object.assign({}, timesheetCpy);
  }

  isEditingTimesheet(timesheet: ActualTimesheet) {
    if (timesheet) {
      return timesheet === this.editTimesheet;
    } else {
      return !!this.editTimesheet;
    }
  }

  private onTimesheetDateChanged(event){
    let wkEnding:any = new Date(event.date.year, event.date.month -1, event.date.day);
    this.createTimeSheetForResource(wkEnding, this.selResourceID);
  }

  private onTimesheetBeginEdit(timesheet: ActualTimesheet) {
    this.editTimesheet = timesheet;
  }

  private updatePlanAndActualHours(resourceID: number){
    let handler = this;
    this.busyloadSingleResource = this.tdlResourceService.getResourceTDLs(resourceID).subscribe(
      data => {
        if (data) {
          handler.resourcetdlbreakhours = data;
          handler.parentSaveTimesheet.next(true);
        }
      },
      error => {
        handler.resetError(true);
        this.alerts.push({ type: "warning",  msg: isNil(error) ? "Error: Unable to get resource info" : error });
      }
    );
  }

  private onTimesheetEndEdit(timesheet: ActualTimesheet, save: boolean) {
    this.isSaving = true;
    let handler = this;

    handler.resetError(false);
    timesheet.weekEnding = DateService.resetTime(timesheet.weekEnding); // moment(timesheet.weekEnding).startOf('day').toDate();
    this.actSheetService
      .saveActualHours(timesheet, this.selResourceID)
      .subscribe(
        data => {
          if (data) {
            handler.arrActualHours = data;
            handler.updateSavedTimeSheet();
            handler.updatePlanAndActualHours(handler.selResourceID);
          }
        },
        error => {
          handler.resetError(true);
          handler.alerts.push({ type: "warning", msg: isNil(error) ? "Error: Unable to save" : error });
        }
      );
  }
}
